import { callForHeader } from '../utils/HeadersCall'
const apiUrl = process.env.REACT_APP_API_URL

export const orderService = {
  getOperatorOrders,
  reserveOrder,
  getOrdersFromMarket,
  payForOrder,
  postOrder,
  deleteOrder,
  getClientOrders,
  getOrders,
  getOrderStatus,
  getSingleOrder,
  getMyOrders
}

const appendSearchParamsToUrl = (url, params) => {
  const newUrl = new URL(url)
  if (params) {
    newUrl.search = new URLSearchParams(params).toString()
  }
  return newUrl
}
async function reserveOrder (guidOrder, guidOperator) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader('json')
  }

  return window.fetch(`${apiUrl}/Order/${guidOrder}/reserve/${guidOperator}`, requestOptions).then(handleResponse)
}

async function getSingleOrder (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return window.fetch(`${apiUrl}/Order/id/${id}`, requestOptions).then(handleResponse)
}

async function getOperatorOrders (guidOperator) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/Order/announced/${guidOperator}`, requestOptions).then(handleResponse)
}

async function getMyOrders (params) {
  const url = appendSearchParamsToUrl(`${apiUrl}/Order/my`, params)
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }
  return await window.fetch(url, requestOptions).then(handleResponse)
}

async function getOrdersFromMarket (params) {
  const url = appendSearchParamsToUrl(`${apiUrl}/Order/my-with-announced`, params)
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }
  return await window.fetch(url, requestOptions).then(handleResponse)
}

async function payForOrder (id) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/Order/id/${id}/pay`, requestOptions).then(handleResponse)
}
async function getOrders (params) {
  const url = appendSearchParamsToUrl(`${apiUrl}/Order`, params)
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return await window.fetch(url, requestOptions).then(handleResponse)
}
async function postOrder (order) {
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader('json'),
    body: JSON.stringify(order)
  }

  return await window.fetch(`${apiUrl}/Order`, requestOptions).then(handleResponse)
}

async function deleteOrder (id) {
  const requestOptions = {
    method: 'DELETE',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/Order/id/${id}`, requestOptions).then(handleResponse)
}

async function getClientOrders (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/Order/client/${id}`, requestOptions).then(handleResponse)
}

async function getOrderStatus (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/Order/id/${id}/status`, requestOptions).then(handleResponse)
}

function handleResponse (response) {
  return response.text().then(text => {
    const data = text && JSON.parse(text)
    if (!response.ok) {
      if (response.status === 404) {
        return []
      }

      const error = (data && data.message) || response.statusText
      return Promise.reject(error)
    }
    const pageCount = response.headers.get('X-Page-Count')
    pageCount && localStorage.setItem('pageCount', pageCount)
    return data
  }).then(data => { return data })
}
