import { operatFilesConstants } from '../constants'
import { operatFiles as initialState } from '../resources'

function operatFiles (state, action) {
  switch (action.type) {
    case operatFilesConstants.FILES_CLEAR_STATE:
      return {
        ...initialState
      }
    case operatFilesConstants.FILES_PUT_OPERAT:
      state.input[action.payload.id].data[action.payload.idx].value = action.payload.event
      return {
        ...state
      }
    case operatFilesConstants.FILES_GET_OPERAT:
      return {
        ...state, disabled: action.payload.disabled, id: action.payload.id, input: action.payload.files
      }
    case operatFilesConstants.FILES_LOAD_OPERAT_TYPE:
      return {
        ...state, input: action.payload.input, photo: action.payload.photo
      }
    case operatFilesConstants.FILES_LOAD_EMPTY_PHOTO:
      return {
        ...state, photo: action.payload
      }
    case operatFilesConstants.FILES_LOAD_EMPTY_INPUT:
      return {
        ...state, input: action.payload
      }
    case operatFilesConstants.FILES_GET_INPUT:
      return {
        ...state, input: action.payload.input
      }
    case operatFilesConstants.FILES_GET_CSV:
      return {
        ...state, csv: action.payload.csv
      }
    case operatFilesConstants.FILES_GET_BY_OPERAT:
      return {
        ...state, id: action.payload.id, files: action.payload.files, operatInfo: action.payload.operatInfo, disabled: true, operatTypeId: action.payload.operatTypeId
      }
    case operatFilesConstants.FILES_LOAD_EMPTY_OPERAT:
      return {
        ...state, disabled: action.payload.disabled, id: action.payload.id
      }
    case operatFilesConstants.FILES_EDIT_OPERAT:
      return {
        ...state, disabled: action.payload.disabled
      }
    case operatFilesConstants.FILES_ADD_LINE_TO_CSV:
      return {
        ...state, csv: state.csv.concat(action.payload.newRow)
      }
    case operatFilesConstants.FILES_ADD_LINES_TO_CSV_FROM_CSV:
      return {
        ...state, csv: state.csv.concat(action.payload.newRows)
      }
    case operatFilesConstants.FILES_REMOVE_LINE_FROM_CSV:
      return {
        ...state, csv: state.csv.filter(item => !action.payload.rows.includes(item.id))
      }
    case operatFilesConstants.FILES_LOAD_PHOTO_FILE_TO_GALLERY:
      if (state.photo.data[action.payload.id].data.some(x => x.id && x.id === action.payload.files.id)) {
        return {
          ...state
        }
      }
      state.photo.data[action.payload.id].data.shift()
      state.photo.data[action.payload.id].data.push(action.payload.files)
      return {
        ...state
      }
    case operatFilesConstants.REMOVE_PHOTOS:
      for (const i of state.photo.data[action.payload.id].data) {
        delete i.file
        delete i.url
        state.files.photo = state.files.photo.filter(x => x.item1 !== i.title)
      }

      return {
        ...state
      }
    case operatFilesConstants.REMOVE_PHOTO_FILES:
      for (const i of state.photo.data[action.payload.id].data) {
        delete i.file
      }
      return {
        ...state
      }
    case 'GENERATE_OPERAT':
      return {
        ...state, generated: [...state.generated, action.payload]
      }
    case 'REMOVE_OPERAT_FROM_GENERATED':
      return {
        ...state, generated: state.generated.filter(item => item.id !== action.payload)
      }
    case 'DOWNLOAD_FROM_EKW':
      var ekw = JSON.parse(action.payload.ekw)
      var input = state.input
      var kw = input.find(x => x.name === action.payload.name)
      Object.entries(ekw).forEach(key => {
        kw.data = kw.data.map(obj => obj.name === key ? { ...obj, value: ekw[key] } : obj)
      })
      input = input.map(x => x.name === action.payload.name ? kw : x)
      return {
        ...state, input
      }
    default:
      return state
  }
}

export default operatFiles
