import React, { useEffect, useState, useContext } from 'react'
import { useMsal } from '@azure/msal-react'
import { Table, Button } from 'reactstrap'
import { AppContext } from '../../contexts/context'
import { operatorFilesService } from '../../services'
import { modalTypeConstants, loadingMessagesConstants } from '../../constants'
import { modalActions } from '../../actions'
import confirm from 'reactstrap-confirm'

function UserUploadedFiles (props) {
  const { accounts } = useMsal()
  const [operatorFiles, setOperatorFile] = useState([])
  const [, dispatch] = useContext(AppContext)

  const generateRows = () => {
    return operatorFiles.map((item, idx) => (
      <tbody key={idx}>
        <tr>
          <th scope='row'>{idx + 1}</th>
          <td>{item.item1}</td>
          <td>
            <Button
              color='primary'
              onClick={() => {
                operatorFilesService
                  .getFile(item.item2, item.item1)
              }}
            >
              pobierz
            </Button>
            {'    '}
            <Button
              color='warning'
              onClick={() => {
                deleteFile(item)
              }}
            >
              Usu
            </Button>
          </td>
        </tr>
      </tbody>
    ))
  }

  const deleteFile = async (item) => {
    const result = await confirm({
      title: <>Ostrzeenie</>,
      message: `Czy na pewno chcesz usun plik ${item.item1}`,
      confirmText: 'Usu',
      confirmColor: 'danger',
      cancelColor: 'link text-primary'
    })
    if (result) {
      modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.DELETE_FILE })
      operatorFilesService
        .removeOperatorFileByName(
          accounts[0].idTokenClaims.oid,
          item.item1
        )
        .then(() => {
          setOperatorFile(
            operatorFiles.filter((obj) => obj.item1 !== item.item1)
          )
          modalActions.hideLoadingScreen(dispatch)
        })
    }
  }

  const files = () => (
    <Table>
      <thead>
        <tr>
          <th>#</th>
          <th>nazwa pliku</th>
          <th>Akcja</th>
        </tr>
      </thead>
      {generateRows()}
    </Table>
  )

  useEffect(() => {
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.LOADING_DATA })
    operatorFilesService
      .getOperatorFiles(accounts[0].idTokenClaims.oid)
      .then((result) => {
        setOperatorFile(result)
        modalActions.hideLoadingScreen(dispatch)
      })
  }, [accounts, dispatch])

  return <>{operatorFiles.length > 0 && files()}</>
}

export default UserUploadedFiles
