import { SET_SELECTED_ORDER, SET_ORDERS, SET_OPERAT_TYPES, ADD_ORDER, REMOVE_ORDER, REPLACE_ORDER, SET_PROPOSITIONS, ADD_PROPOSITION, REPLACE_PROPOSITION } from './constants'
const initialState = {
  selectedOrder: {},
  orders: [],
  operatTypes: [],
  propositions: []
}

const marketPage = (state = initialState, action) => {
  switch (action.type) {
    case SET_SELECTED_ORDER: {
      return {
        ...state,
        selectedOrder: action.payload
      }
    }
    case SET_ORDERS: {
      return {
        ...state,
        orders: action.payload
      }
    }
    case ADD_ORDER: {
      return {
        ...state,
        orders: [action.payload].concat(state.orders)
      }
    }
    case REMOVE_ORDER: {
      return {
        ...state,
        orders: state.orders.filter(x => x.id !== action.payload)
      }
    }
    case REPLACE_ORDER: {
      return {
        ...state,
        selectedOrder: action.payload,
        orders: state.orders.filter(x => x.id !== action.payload.id).concat(action.payload)
      }
    }
    case SET_OPERAT_TYPES: {
      return {
        ...state,
        operatTypes: action.payload
      }
    }
    case SET_PROPOSITIONS: {
      return {
        ...state,
        propositions: action.payload
      }
    }
    case ADD_PROPOSITION: {
      return {
        ...state,
        propositions: state.propositions.concat(action.payload)
      }
    }
    case REPLACE_PROPOSITION: {
      return {
        ...state,
        propositions: state.propositions.filter(x => x.id !== action.payload.id).concat(action.payload)
      }
    }
    default:
      return state
  }
}

export default marketPage
