import { loginRequest } from '../authConfig'
import { msalInstance } from '../index'

export const shouldRefreshToken = () => {
  const currentUnixTimeInSeconds = parseInt(new Date().getTime() / 1000)
  const tokenWillExpireSoon = localStorage.getItem('idToken') && localStorage.getItem('idTokenExp') && parseInt(localStorage.getItem('idTokenExp')) < currentUnixTimeInSeconds + 60
  return !localStorage.getItem('idToken') || !localStorage.getItem('idTokenExp') || tokenWillExpireSoon
}
export async function callForHeader (type) {
  const account = msalInstance.getActiveAccount()
  if (!account) {
    throw Error('No active account! Verify a user has been signed in and setActiveAccount has been called.')
  }

  const request = {
    ...loginRequest,
    account: account
  }
  let response
  if (shouldRefreshToken()) {
    response = await msalInstance.acquireTokenSilent(request).catch(error => {
      // fallback to interaction when silent call fails
      console.log(error)
      return msalInstance.acquireTokenRedirect(request).then(tokenResponse => {
        return tokenResponse
      }).catch(error => {
        console.log(error)
      })
    })

    localStorage.setItem('idToken', response.idToken)
    localStorage.setItem('idTokenExp', response.idTokenClaims.exp)
  } else {
    response = { idToken: localStorage.getItem('idToken') }
  }

  const headers = new Headers()
  const bearer = `Bearer ${response.idToken}`
  headers.append('Authorization', bearer)

  let content
  switch (type) {
    case 'json':
      content = 'application/json'
      headers.append('Content-Type', content)
      break
    case 'plain':
      content = 'text/plain;charset=UTF-8'
      headers.append('Content-Type', content)
      break
    default:
      break
  }

  return headers
}
