import { useMsal } from '@azure/msal-react'
import React, { useContext, useEffect, useRef, useState } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import { useTranslation } from 'react-i18next'
import {
  Button, Card, CardBody, Col, Collapse, Input, Form,
  FormGroup,
  Label, Row
} from 'reactstrap'
import { userRolesConstants } from '../../constants'
import { AppContext } from '../../contexts/context'
import { useHistory } from 'react-router'
import Pagination from '@mui/material/Pagination'
import UserInfoUncontrolledCollapse from '../Common/OrderInfoUncontrolledCollapse'
import AnnouncedOrder from './AnnouncedOrder'
import NewOrderForm from 'components/NewOrderForm'
import PaidOrder from './PaidOrder'
import ReservedOrder from './ReservedOrder'
import SignedOrder from './SignedOrder'
import { addOrder, appendFiltersToUrl, getOrders, selectOrder } from './actions'
import MarketFilters from 'components/Filters/Market'
function Market (props) {
  const columns = () => {
    return [
      {
        dataField: 'id',
        text: 'ID',
        hidden: isIdHidden
      },
      {
        dataField: 'inProcess',
        text: t('market.table.headers.inProgress'),
        editable: false,
        align: 'center',
        headerAlign: 'center',
        formatter: (cellContent, row) => (
          <Input type='checkbox' checked={row.status.toLowerCase() === 'inprogress'} disabled />
        ),
        headerStyle: (colum, colIndex) => {
          return { width: '10%', textAlign: 'center' }
        }
      },
      {
        dataField: 'createdDateTime',
        text: t('market.table.headers.applicationDate'),
        align: 'center',
        headerAlign: 'center',
        editable: false,
        formatter: (cellContent, row) => <div id={row.id}>{cellContent.split('T')[0]}</div>,
        headerStyle: (colum, colIndex) => {
          return { width: '15%', textAlign: 'center' }
        }
      },
      {
        dataField: 'name',
        text: t('market.table.headers.name'),
        align: 'center',
        headerAlign: 'center',
        editable: false,
        formatter: (cellContent, row) => (
          <div>
            {cellContent.length > 30
              ? cellContent.substring(0, 30) + '...'
              : cellContent}
          </div>
        )
      },
      {
        dataField: 'locationPostCode',
        text: t('market.table.headers.postCode'),
        align: 'center',
        headerAlign: 'center',
        editable: false,
        formatter: (cellContent) => (
          <div>{cellContent}</div>
        )
      },
      {
        dataField: 'status',
        text: t('market.table.headers.status'),
        align: 'center',
        headerAlign: 'center',
        editable: false,
        formatter: (cellContent) => (
          <div>{t(`market.table.statuses.${cellContent}`)}</div>
        )
      }
    ]
  }

  const renderSwitch = (row) => {
    switch (row.status) {
      case 'announced':
        return <AnnouncedOrder />
      case 'reserved':
        return <ReservedOrder />
      case 'paid':
        return !isClient && (row.operators[0] === accounts[0].idTokenClaims.oid) && <PaidOrder />
      case 'signed':
        return <SignedOrder />
      default:
        break
    }
  }
  const [expanded, setExpanded] = useState('')
  const history = useHistory()
  const expandRow = () => {
    return {
      onlyOneExpanding: true,
      expanded: [expanded],
      renderer: (row) => (
        <div style={{ wordBreak: 'break-all' }}>
          <div>
            <Button
              className='long-btn'
              color='success'
              id='orderData'
              style={{ marginBottom: '1rem' }}
            >
              {t('market.table.buttons.orderData')}
            </Button>
            <UserInfoUncontrolledCollapse row={row} />
          </div>
          {renderSwitch(row)}
        </div>
      ),
      onExpand: (row) => {
        expanded === row.id ? setExpanded('') : setExpanded(row.id)
        const search = new URLSearchParams(history.location.search)
        search.set('id', row.id)
        history.push({
          pathname: '/market',
          search: search.toString()
        })
        selectOrder(dispatch, row)
      }
    }
  }
  const { t } = useTranslation('common')
  const [newOrder, setNewOrder] = useState(false)
  const newOrderButton = useRef(null)
  const { accounts } = useMsal()
  const [state, dispatch] = useContext(AppContext)
  const [isFiltersOpen, setIsFiltersOpen] = useState(false)
  const [isIdHidden, setIsIdHidden] = useState(true)
  const isAdmin = state.user.userType === userRolesConstants.ADMIN
  const isClient = state.user.userType === userRolesConstants.CLIENT

  useEffect(() => {
    getOrders(dispatch, state.user.userType, history.location, setExpanded)
  // eslint-disable-next-line
  }, [history.location.search])

  const updateMarket = () => {
    setNewOrder(!newOrder)
  }

  const handleChange = (event, newPage) => {
    const search = new URLSearchParams(history.location.search)
    search.set('pageNumber', newPage.toString())
    history.push({
      pathname: '/market',
      search: search.toString()
    })
  }

  return (
    <div className='content'>
      <div className='container'>
        <Button
          color='primary'
          onClick={() => {
            setNewOrder(!newOrder)
          }}
          innerRef={newOrderButton}
        >
          {t('market.table.buttons.newOrder')}
        </Button>
        <Collapse isOpen={newOrder}>
          <Card>
            <CardBody>
              <NewOrderForm
                updateMarket={() => {
                  updateMarket()
                }}
                onSubmit={(newOrder) => addOrder(dispatch, newOrder)}
                newOrderButton={newOrderButton}
              />
            </CardBody>
          </Card>
        </Collapse>
        <br />
        <Button color='success' onClick={() => setIsFiltersOpen(!isFiltersOpen)}>Filtry</Button>
        <Collapse isOpen={isFiltersOpen}>
          <MarketFilters
            onSubmit={(e) => {
              appendFiltersToUrl(e, history)
            }}
            onClear={() => {
              const search = new URLSearchParams(history.location.search)
              search.delete('locationPostCode')
              history.push({
                pathname: '/market',
                search: search.toString()
              })
            }}
          />
        </Collapse>
        {isAdmin && <Form>
          <FormGroup>
            <Label>
              {t('market.showOrderId')}
              <input
                type='checkbox'
                style={{ marginLeft: '0.5rem' }}
                onChange={() => {
                  setIsIdHidden(!isIdHidden)
                }}
              />{' '}
            </Label>
          </FormGroup>
        </Form>}
        <Row>
          <Col md='12'>
            <Pagination count={localStorage.getItem('pageCount')} color='primary' page={(new URLSearchParams(history.location.search)).get('pageNumber') ? parseInt((new URLSearchParams(history.location.search)).get('pageNumber')) : 1} onChange={handleChange} />
            <BootstrapTable
              bootstrap4
              keyField='id'
              data={state.marketPage.orders}
              columns={columns(accounts)}
              striped
              hover
              condensed
              bordered={false}
              expandRow={expandRow()}
            />
          </Col>
        </Row>
      </div>
    </div>
  )
}

export default Market
