import React, { useContext, useState, useEffect } from 'react'
import { operatService, clientFilesService } from 'services'
import { modalActions } from '../../actions'
import { AppContext } from 'contexts/context'
import {
  Button, Col, FormGroup, Input, Label
} from 'reactstrap'
import { modalTypeConstants, loadingMessagesConstants } from '../../constants'
import ReviewForm from 'components/ReviewForm'
import { addReview } from './actions'
import { useHistory } from 'react-router-dom'

function SignedOrder (props) {
  const [state, dispatch] = useContext(AppContext)
  const [operat, setOperat] = useState(null)
  const history = useHistory()
  const downloadSignedOperat = async () => {
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.DOWNLOAD_OPERAT })
    clientFilesService.getFiles(state.marketPage.selectedOrder.clientId).then(response => {
      for (const file of response) {
        if (file.item2.includes(state.marketPage.selectedOrder.id + '.pdf')) {
          modalActions.hideLoadingScreen(dispatch)
          window.open(
            file.item2, '_blank')
          return
        }
      }
      alert('nie znaleziono pliku pdf')
    }).finally(() => modalActions.hideLoadingScreen(dispatch))
  }
  useEffect(() => {
    operatService.getByOrderId(state.marketPage.selectedOrder.id).then((res) => {
      setOperat(res)
    })
  }, [state.marketPage.selectedOrder])
  return (
    <>
      <Button
        style={{ width: '272px', marginBottom: '1rem' }}
        color='primary'
        onClick={async () => await downloadSignedOperat()}
      >
        Pobierz operat
      </Button>
      <br />
      <Button
        color='info' style={{ width: '272px', marginBottom: '1rem' }} onClick={() => {
          history.push({ pathname: '/users-profile', search: '?operatorId=' + state.marketPage.selectedOrder.operators[0] })
        }}
      >Dane o rzeczoznawcy
      </Button>
      <Col
        sm={{
          size: 4
        }}
      >
        <FormGroup>
          <Input
            id='checkbox2'
            type='checkbox'
            onChange={() => {
              modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: 'aaa' })
              operatService.obstructOperat(operat.id).then(() => {
                setOperat({ ...operat, isObstructed: !operat.isObstructed })
              }).finally(() => modalActions.hideLoadingScreen(dispatch))
            }}
            checked={operat && operat.isObstructed}
          />
          {' '}
          <Label>
            Operat zakwestionowany
          </Label>
        </FormGroup>
      </Col>
      {operat && <ReviewForm operat={operat} onSubmit={(e, rating) => addReview(e, rating, state.marketPage.selectedOrder, operat.id, dispatch)} />}
    </>
  )
}

export default SignedOrder
