import React, { useState, useContext, useRef } from 'react'
import { AppContext } from '../../contexts/context'
import NotificationAlert from 'react-notification-alert'

import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Badge
} from 'reactstrap'
import { operatFilesActions } from '../../actions'

function MessageBell (props) {
  const [open, setOpen] = useState(false)
  const [state, dispatch] = useContext(AppContext)

  let options = {}
  options = {
    place: 'br',
    message: (
      <div>
        <div>
          Wiadomo zostaa przeczytana
        </div>
      </div>
    ),
    type: 'warning',
    icon: 'nc-icon nc-check-2',
    autoDismiss: 5
  }

  const notifyRegister = useRef(null)
  const startNotify = (item) => {
    options.message = (
      <div>
        <div>
          <b>{item.value}</b>     Wiadomo zostaa przeczytana
        </div>
      </div>
    )
    notifyRegister.current.notificationAlert(options)
  }

  const handlePickMessage = (item) => {
    operatFilesActions.removeInfo(dispatch, item.id)
    startNotify(item)
  }

  const dropdownToggle = (e) => {
    setOpen(!open)
  }

  return (
    <div>
      <NotificationAlert ref={notifyRegister} />
      <Dropdown nav isOpen={open} toggle={(e) => dropdownToggle(e)}>
        <DropdownToggle caret nav>
          <i className='nc-icon nc-bell-55' />
          {state.operatFiles && state.operatFiles.generated.length > 0
            ? (
              <Badge color='danger' pill>
                {state.operatFiles.generated.length}
              </Badge>
              )
            : null}
          <p>
            <span className='d-lg-none d-md-block'>Wiadomoci</span>
          </p>
        </DropdownToggle>
        <DropdownMenu right>
          {state.operatFiles && state.operatFiles.generated.length > 0
            ? (
                state.operatFiles.generated.map((item, id) => {
                  return (
                    <DropdownItem
                      key={id}
                      tag='a'
                      onClick={() => {
                        handlePickMessage(item)
                      }}
                    >
                      {item.value}
                    </DropdownItem>
                  )
                })
              )
            : (
              <DropdownItem tag='a'>Nie ma wiadomoci</DropdownItem>
              )}
        </DropdownMenu>
      </Dropdown>
    </div>
  )
}

export default MessageBell
