import React, { useState, useContext } from 'react'

import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap'
import UserFilesTab from './UserFilesTab'
import EditUser from './EditUser'
import UserProfileTab from './UserProfileTab'
import { useLocation } from 'react-router'
import classnames from 'classnames'
import OperatorTrainingsTab from './OperatorTrainingsTab'
import { AppContext } from 'contexts/context'
import { userRolesConstants } from '../../constants'
function Users (props) {
  const [activeTab, setActiveTab] = useState('1')
  const [state] = useContext(AppContext)
  const toggle = (tab) => {
    if (activeTab !== tab) setActiveTab(tab)
  }
  const location = useLocation()
  const isClient = state.user.userType === userRolesConstants.CLIENT
  const createNavItem = (tabNr, label) =>
    <NavItem>
      <NavLink
        className={classnames({ active: activeTab === tabNr })}
        onClick={() => {
          toggle(tabNr)
        }}
      >
        {label}
      </NavLink>
    </NavItem>
  return (
    <>
      <div className='content'>
        <Nav tabs>
          {createNavItem('1', 'Uytkownik')}
          {!location.search &&
            <>
              {createNavItem('2', 'Edycja profilu')}
              {!isClient && createNavItem('3', 'Pliki')}
              {!isClient && createNavItem('4', 'Szkolenia')}
            </>}
        </Nav>
        <TabContent activeTab={activeTab}>
          <TabPane tabId='1'>
            <UserProfileTab />
          </TabPane>
          <TabPane tabId='2'>
            <EditUser />
          </TabPane>
          {!isClient && <TabPane tabId='3'>
            <UserFilesTab />
          </TabPane>}
          <TabPane tabId='4'>
            <OperatorTrainingsTab />
          </TabPane>
        </TabContent>
      </div>
    </>
  )
}

export default Users
