import { useMsal } from '@azure/msal-react'
import { AppContext } from 'contexts/context'
import React, { useContext, useState } from 'react'
import { useTranslation } from 'react-i18next'
import {
  Button, Card, CardBody, Collapse
} from 'reactstrap'
import { userRolesConstants } from '../../constants'
import UserInfo from '../../views/Common/UserInfo'
import NewOffer from 'components/NewOfferForm'
import PropositionList from './PropositionList'
import { addProposition, deleteOrder } from './actions'
function AnnouncedOrder (props) {
  const [state, dispatch] = useContext(AppContext)
  const [sendedOffers, setSendedOffers] = useState(false)
  const [userInfo, setUserInfo] = useState(false)
  const [offer, setOffer] = useState(false)
  const { t } = useTranslation('common')
  const { accounts } = useMsal()

  return (
    <div>
      {(userRolesConstants.ADMIN === state.user.userType ||
      (state.user.userType === userRolesConstants.OPERATOR && accounts[0].idTokenClaims.oid !== state.marketPage.selectedOrder.clientId)) && (
        <div>
          <Button
            color='primary'
            className='long-btn'
            onClick={() => {
              setOffer(!offer)
            }} style={{ marginBottom: '1rem' }}
          >
            {t('market.table.buttons.submittOffer')}
          </Button>{' '}
          <Collapse isOpen={offer}>
            {offer && <NewOffer onSubmit={(e) => addProposition(dispatch, e)} hide={() => setOffer(false)} />}
          </Collapse>
        </div>
      )}
      <div>
        <Button
          className='long-btn proposition-list-toggler'
          color='secondary'
          style={{ marginBottom: '1rem' }}
          onClick={() => {
            setSendedOffers(!sendedOffers)
          }}
        >
          {t('market.table.buttons.submittedOffers')}
        </Button>
        <Collapse isOpen={sendedOffers}>
          <Card>
            <CardBody>
              {sendedOffers && <PropositionList />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
      {
           accounts[0].idTokenClaims.oid !== state.marketPage.selectedOrder.clientId &&
             <div>
               <Button
                 color='info'
                 className='long-btn'
                 onClick={() => {
                   setUserInfo(!userInfo)
                 }}
                 style={{ marginBottom: '1rem' }}
               >
                 {t('market.table.buttons.payerInformation')}
               </Button>

               <Collapse isOpen={userInfo}>
                 <Card>
                   <CardBody>
                     {userInfo && <UserInfo id={state.marketPage.selectedOrder.clientId} />}
                   </CardBody>
                 </Card>
               </Collapse>
             </div>
        }
      <Button
        className='long-btn'
        color='danger'
        disabled={state.marketPage.selectedOrder.clientId !== accounts[0].idTokenClaims.oid}
        onClick={() => deleteOrder(dispatch, state.marketPage.selectedOrder.id, t)}
      >
        {t('market.table.buttons.deleteOrder')}
      </Button>
    </div>
  )
}

export default AnnouncedOrder
