import React, { useState } from 'react'
import {
  Card,
  CardBody,
  Input,
  Button,
  Form
} from 'reactstrap'

export const MarketFilters = ({ onSubmit, onClear }) => {
  const [locationPostCode, setLocationPostCode] = useState('')
  const handleClear = () => {
    setLocationPostCode('')
    onClear()
  }
  return (
    <Card>
      <CardBody>
        <Form>
          <Input
            placeholder='Kod pocztowy' id='filter-locationPostCode' name='filter-locationPostCode' onChange={(e) => {
              setLocationPostCode(e.target.value)
              onSubmit(e)
            }} value={locationPostCode}
          />
          <Button onClick={handleClear}>Wyczy</Button>
        </Form>
      </CardBody>
    </Card>
  )
}

export default MarketFilters
