import React, { useContext, useEffect, useState } from 'react'
import { useMsal } from '@azure/msal-react'
import {
  Card,
  CardBody,
  Row,
  Col,
  Accordion,
  AccordionItem,
  AccordionBody,
  AccordionHeader
} from 'reactstrap'
import { AppContext } from '../../contexts/context'
import { useLocation } from 'react-router'
import { loadingMessagesConstants, modalTypeConstants } from '../../constants'
import defaultAvatar from '../../assets/img/default-avatar.png'
import fakeImg from '../../assets/img/damir-bosnjak.jpg'
import ReadonlyTable from 'components/Tables/ReadonlyTable'
import { operatorService, operatorFilesService, operatService } from 'services'
import { modalActions } from 'actions'

function UserProfileTab (props) {
  const { accounts } = useMsal()
  const [state, dispatch] = useContext(AppContext)
  const [operator, setOperator] = useState(null)
  const [diplomaUrl, setDiplomaUrl] = useState('')
  const [openAccordion, setOpenAccordion] = useState('')
  const [reviews, setReviews] = useState([])
  const [trainings, setTrainings] = useState([])
  const [operatCount, setOperatCount] = useState(0)
  const createInZpbInformation = () => {
    if (!operator) return null
    const text = 'Operator ' + operator.name + ' ' + (!operator.inZbpList ? 'nie ' : '') + 'jest zapisany do listy rzeczoznawcw ZBP.'
    return <p>{text}</p>
  }
  const location = useLocation()
  useEffect(() => {
    if (location.search) {
      modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.OPERATOR_PROFILE })
      const params = new URLSearchParams(location.search)
      const operatorId = params.get('operatorId')
      operatorService.getOperatorData(operatorId).then(async (res) => {
        setOperator(res)
        await operatorFilesService.getFileByFilename(operatorId, 'diploma.jpg').then((res) => setDiplomaUrl(res)).catch((err) => console.log(err))
        await operatService.getOperatorOperats(operatorId).then((res) => setOperatCount(res.length)).catch((err) => console.log(err))
        await operatorService.getOperatorReviews(operatorId).then((res) => setReviews(res.map(x => ({
          created: x.created.substring(0, 10),
          client: x.clientId,
          comment: x.comment,
          rating: x.rating
        })
        ))).catch(err => console.log(err))
        await operatorService.getOperatorTrainings(operatorId).then((res) => setTrainings(res.map(x => ({ title: x.title, url: x.url }))))
        modalActions.hideLoadingScreen(dispatch)
      }).catch(err => {
        console.log(err)
        modalActions.hideLoadingScreen(dispatch)
      })
    }
  }, [location, dispatch])

  return (
    <>
      <Row>
        <Col md='12'>
          <Card className='card-user'>
            <div className='image'>
              <img alt='...' src={fakeImg} />
            </div>
            <CardBody>
              <div className='author'>
                <a href='/users-profile' onClick={(e) => e.preventDefault()}>
                  <img
                    alt='...'
                    className='avatar border-gray'
                    src={defaultAvatar}
                  />
                  {!location.search
                    ? <div>
                      <h5 className='title'>{state.operator.name}</h5>
                      <h5 className='title'>
                        {accounts[0].idTokenClaims.given_name}{' '}
                        {accounts[0].idTokenClaims.family_name}
                      </h5>
                      <h5 className='title'>{state.operator.mail}</h5>
                    </div>
                    : <h5 className='title'>{operator && operator.name}</h5>}
                </a>
                {
                  location.search &&
                    <div>
                      <Accordion
                        open={openAccordion}
                        toggle={(e) => e === openAccordion ? setOpenAccordion('') : setOpenAccordion(e)}
                      >
                        <AccordionItem>
                          <AccordionHeader targetId='1'>
                            Dyplom
                          </AccordionHeader>
                          <AccordionBody accordionId='1'>
                            {diplomaUrl === '' || typeof diplomaUrl !== 'string' ? 'operator nie wgra dyplomu do systemu' : <img src={diplomaUrl} alt='dyplom' />}
                          </AccordionBody>
                        </AccordionItem>
                        <AccordionItem>
                          <AccordionHeader targetId='2'>
                            Przebyte szkolenia
                          </AccordionHeader>
                          <AccordionBody accordionId='2'>
                            {trainings.length > 0
                              ? <ReadonlyTable
                                  headers={['Tytu', 'Adres url']}
                                  data={trainings}
                                />
                              : 'Brak danych'}
                          </AccordionBody>
                        </AccordionItem>
                        <AccordionItem>
                          <AccordionHeader targetId='3'>
                            Kontakt
                          </AccordionHeader>
                          <AccordionBody accordionId='3'>
                            <ul style={{ textAlign: 'left', fontSize: '1.5rem' }}>
                              {operator && <li>Adres e-mail: <a href={'mailto:' + operator.mail}>{operator.mail}</a></li>}
                            </ul>
                          </AccordionBody>
                        </AccordionItem>
                        <AccordionItem>
                          <AccordionHeader targetId='4'>
                            Opinie zleceniodawcw
                          </AccordionHeader>
                          <AccordionBody accordionId='4'>
                            {reviews.length > 0
                              ? <ReadonlyTable
                                  headers={['Data', 'Zleceniodawca', 'Komentarz', 'Ocena']}
                                  data={reviews}
                                />
                              : 'Brak danych'}
                          </AccordionBody>
                        </AccordionItem>
                        <AccordionItem>
                          <AccordionHeader targetId='5'>
                            Pozostae dane
                          </AccordionHeader>
                          <AccordionBody accordionId='5' style={{ textAlign: 'left', fontSize: '1rem' }}>
                            {createInZpbInformation()}
                            Liczba wykonanych operatw: {operatCount}
                          </AccordionBody>
                        </AccordionItem>
                      </Accordion>
                    </div>
                }
              </div>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </>
  )
}

export default UserProfileTab
