import { useMsal } from '@azure/msal-react'
import React, { useContext, useState } from 'react'
import { useTranslation } from 'react-i18next'
import {
  Button, Card,
  CardBody, Form,
  FormGroup, Input, Label
} from 'reactstrap'
import { AppContext } from '../../contexts/context'
import { validators } from '../../helpers'

export const NewOffer = (props) => {
  const { t } = useTranslation('common')
  const { accounts } = useMsal()
  const [state] = useContext(AppContext)
  const [description, setDescription] = useState('')
  const [price, setPrice] = useState('')
  const [maxDate, setMaxDate] = useState('')

  return (
    <Card>
      <CardBody>
        <Form
          onSubmit={(e) => {
            e.preventDefault()
            const { exampleNumber, exampleDate } = e.target.elements
            const data = {
              orderId: state.marketPage.selectedOrder.id,
              description:
                description !== ''
                  ? description
                  : `${exampleNumber.value} PLN do dnia ${exampleDate.value}`,
              operatorId: accounts[0].idTokenClaims.oid,
              price: exampleNumber.value.toString(),
              upTillDate: exampleDate.value
            }
            props.onSubmit(data)
            props.hide()
          }}
        >
          <div className='row'>
            <div className='col-6'>
              <div className='justify-content-center'>
                <FormGroup>
                  <Label for='exampleNumber'>{t('common.userInfo.description')}</Label>
                  <Input
                    type='textarea'
                    name='description'
                    id='description'
                    placeholder={t('common.userInfo.orderDescription')}
                    onChange={(e) => {
                      setDescription(e.target.value)
                    }}
                  />
                </FormGroup>
                <FormGroup>
                  <Label for='exampleNumber'>{t('common.userInfo.offeredPrice')}</Label>
                  <Input
                    type='number'
                    id='exampleNumber'
                    min={10}
                    max={9999999}
                    onKeyPress={(e) => validators.validatePositiveNumbers(e, /^[0-9]{0,10}$/)}
                    placeholder={t('common.userInfo.offeredPrice')}
                    onChange={(e) => {
                      if (e.target.value.startsWith('-') || e.target.value < 0 || e.target.value === '') {
                        e.target.value = ''
                      }
                      e.target.value >= 0 && setPrice(e.target.value)
                    }}
                    required
                  />
                </FormGroup>
                <FormGroup>
                  <Label for='exampleDate'>{t('common.userInfo.endDate')}</Label>
                  <Input
                    type='date'
                    name='date'
                    id='exampleDate'
                    placeholder='date placeholder'
                    onChange={(e) => {
                      setMaxDate(e.target.value)
                    }}
                    required
                  />
                </FormGroup>
              </div>
            </div>
            <div className='col-4 d-flex justify-content-center align-items-center'>
              <Button color='success' disabled={price === '' || maxDate === ''}>
                {t('common.userInfo.makeAnOffer')}
              </Button>
            </div>
          </div>
        </Form>
      </CardBody>
    </Card>
  )
}

export default NewOffer
