import { SET_OPERATS, REMOVE_OPERAT } from './constants'
const initialState = {
  operats: []
}

const historyPage = (state = initialState, action) => {
  switch (action.type) {
    case SET_OPERATS: {
      return {
        ...state,
        operats: action.payload
      }
    }
    case REMOVE_OPERAT: {
      return {
        ...state,
        operats: state.operats.filter(x => x.id !== action.payload)
      }
    }
    default:
      return state
  }
}

export default historyPage
