import React, { useContext, useEffect, useState } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import { useTranslation } from 'react-i18next'
import {
  Col, Form,
  FormGroup,
  Label, Row
} from 'reactstrap'
import { userRolesConstants } from '../../constants'
import { AppContext } from '../../contexts/context'
import { getOperats } from './actions'
import HistoryRow from './HistoryRow'
import Pagination from '@mui/material/Pagination'
import { useHistory } from 'react-router'

function History (props) {
  const { t } = useTranslation('common')
  const history = useHistory()
  const [state, dispatch] = useContext(AppContext)
  const [isIdHidden, setIsIdHidden] = useState(true)
  const isAdmin = state.user.userType === userRolesConstants.ADMIN

  useEffect(() => {
    getOperats(dispatch, history.location)
  }, [dispatch, history.location])

  const columns = [
    {
      dataField: 'id',
      text: 'ID',
      hidden: isIdHidden
    },
    {
      dataField: 'inProcess',
      text: t('history.table.headers.inProgress'),
      editable: false,
      headerStyle: { width: '15%' },
      align: 'center',
      headerAlign: 'center',
      formatter: (cellContent, row) => (
        <label>
          <input type='checkbox' defaultChecked={row.inProcess} />
        </label>
      )
    },
    {
      dataField: 'createdDateTime',
      text: t('history.table.headers.applicationDate'),
      align: 'center',
      headerAlign: 'center',
      editable: false,
      formatter: (cellContent, row) => <div>{cellContent.split('T')[0]}</div>,
      headerStyle: (colum, colIndex) => {
        return { width: '15%', textAlign: 'center' }
      }
    },
    {
      dataField: 'name',
      text: t('history.table.headers.informations'),
      headerAlign: 'center',
      align: 'center',
      editable: false
    }
  ]

  const expandRow = () => {
    return {
      onlyOneExpanding: true,
      onExpand: (row, isExpand, rowIndex, e) => {
      },
      renderer: (row) => (
        <HistoryRow row={row} history={props.history} />
      )
    }
  }
  const handleChange = (event, newPage) => {
    const search = new URLSearchParams(history.location.search)
    search.set('pageNumber', newPage.toString())
    history.push({
      pathname: '/history',
      search: search.toString()
    })
  }
  return (
    <div className='content'>
      <div className='container'>
        {isAdmin && <Form>
          <FormGroup>
            <Label>
              Pokaza id operatu?
              <input
                type='checkbox'
                style={{ marginLeft: '0.5rem' }}
                onChange={() => {
                  setIsIdHidden(!isIdHidden)
                }}
              />{' '}
            </Label>
          </FormGroup>
        </Form>}
        <Row>
          <Col md='12'>
            <Pagination count={localStorage.getItem('pageCount')} color='primary' page={(new URLSearchParams(history.location.search)).get('pageNumber') ? parseInt((new URLSearchParams(history.location.search)).get('pageNumber')) : 1} onChange={handleChange} />
            <BootstrapTable
              bootstrap4
              keyField='id'
              data={state.historyPage.operats}
              columns={columns}
              striped
              hover
              condensed
              bordered={false}
              expandRow={expandRow()}
            />
          </Col>
        </Row>
      </div>
    </div>
  )
}

export default History
