import React, { useContext, useEffect, useRef, useState } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import cellEditFactory from 'react-bootstrap-table2-editor'
import ToolkitProvider, { CSVExport } from 'react-bootstrap-table2-toolkit'
import { useTranslation } from 'react-i18next'
import { Button, Card, CardBody, Col, Row } from 'reactstrap'
import confirm from 'reactstrap-confirm'
import { modalActions, operatFilesActions } from '../../actions'
import { loadingMessagesConstants, modalTypeConstants } from '../../constants'
import { AppContext } from '../../contexts/context'
import { operatFilesService } from '../../services'

function CsvComponent ({ reloadOperat, operat }) {
  const { t } = useTranslation('common')
  const [state, dispatch] = useContext(AppContext)
  const [selectedRows, setSelectedRows] = useState([])
  const [csvToRead, setCsvToRead] = useState()
  const inputFile = useRef(null)
  const cols = [
    {
      dataField: 'id',
      text: 'ID',
      hidden: true
    },
    {
      dataField: 'Data',
      text: 'Data',
      editable: true,
      align: 'center',
      headerAlign: 'center',
      sort: true
    },
    {
      dataField: 'CenaTransakcyjna',
      text: 'Cena Transakcyjna',
      align: 'center',
      headerAlign: 'center',
      editable: true,
      sort: true
    },
    {
      dataField: 'RodzajRynku',
      text: 'Rodzaj Rynku',
      headerAlign: 'center',
      align: 'center',
      sort: true,
      editable: true
    },
    {
      dataField: 'UdzialWPrawie',
      text: 'Udzial W Prawie',
      headerAlign: 'center',
      align: 'center',
      editable: true,
      sort: true
    },
    {
      dataField: 'PowierzchniaLokalu',
      text: 'Powierzchnia Lokalu',
      headerAlign: 'center',
      align: 'center',
      editable: true,
      sort: true
    },
    {
      dataField: 'Adres',
      text: 'Adres',
      headerAlign: 'center',
      align: 'center',
      editable: true,
      sort: true
    },
    {
      dataField: 'KondygnacjaLokalu',
      text: 'Kondygnacja Lokalu',
      headerAlign: 'center',
      align: 'center',
      editable: true,
      sort: true
    },
    {
      dataField: 'KondygnacjaBudynku',
      text: 'Kondygnacja Budynku',
      headerAlign: 'center',
      align: 'center',
      editable: true,
      sort: true
    },
    {
      dataField: 'LiczbaIzb',
      text: 'Liczba Izb',
      headerAlign: 'center',
      align: 'center',
      editable: true,
      sort: true
    }
  ]

  useEffect(() => {
    const csvItems = state.operatFiles.files.csv
    const load = () => {
      modalActions.showLoadingScreen(
        dispatch,
        modalTypeConstants.LOADING,
        { message: loadingMessagesConstants.LOADING_DATA }
      )
      operatFilesActions.loadOperatCSV(dispatch, csvItems.item2)
    }
    !!csvItems && Object.keys(csvItems).length > 0 && load()
  }, [state.operatFiles.files, dispatch])

  const selectRow = () => {
    const select = {
      mode: 'checkbox',
      clickToEdit: true,
      clickToSelect: true,
      onSelect: (row, isSelect, rowIndex, e) => {
        isSelect && setSelectedRows((allRows) => [...allRows, row.id])
        !isSelect &&
          setSelectedRows(selectedRows.filter((item) => item !== row.id))
      },
      onSelectAll: (isSelect, rows) => {
        !isSelect && setSelectedRows([])
        isSelect &&
          setSelectedRows(
            rows.map((row) => {
              return row.id
            })
          )
      }
    }
    return select
  }
  const cellEdit = cellEditFactory({
    mode: 'dbclick'
  })
  const { ExportCSVButton } = CSVExport

  const processCSV = (str) => {
    const rows = str.slice(str.indexOf('\n') + 1).split('\n').filter(x => x !== '')
    const EXPECTED_COUNT_OF_COLUMNS = 9
    if (
      rows.some(
        (row) =>
          (row.match(/;/g) || []).length !== EXPECTED_COUNT_OF_COLUMNS - 1
      )
    ) {
      const columns = str.split('\n')[0].split(';').map(item => item.trim()).filter(x => x !== '')
      const requiredColumns = cols.filter(x => x.text !== 'ID').map(x => { return x.dataField }).filter(x => !columns.includes(x.trim()))
      showWarning(EXPECTED_COUNT_OF_COLUMNS, requiredColumns)
      return
    }
    operatFilesActions.addLinesToCSVFromCSV(dispatch, rows)
  }

  const submit = () => {
    const file = csvToRead
    const reader = new FileReader()

    reader.onload = function (e) {
      const text = e.target.result
      processCSV(text)
    }

    reader.readAsText(file)
  }

  const showWarning = async (EXPECTED_COUNT_OF_COLUMNS, requiredColumns) => {
    const mess = `
      Liczba kolumn w pliku si nie zgadza.
      Plik  powinien zawiera ${EXPECTED_COUNT_OF_COLUMNS} kolumn.
      Brakujce kolumny to : ${requiredColumns.map(x => x)}
      `
    await confirm({
      title: <>{t('history.table.confirm.warning')}</>,
      message: mess,
      confirmText: 'Wr',
      cancelText: '',
      confirmColor: 'danger',
      cancelColor: 'link text-primary'
    })
  }

  return (
    <>
      <Card>
        <CardBody>
          <br />
          <br />
          <br />
          <br />
          <div className='container'>
            <Row>
              <Col md='12'>
                <Card>
                  <ToolkitProvider
                    keyField='id'
                    data={state.operatFiles.csv}
                    columns={cols}
                    exportCSV={{ onlyExportSelection: true, exportAll: true }}
                  >
                    {(props) => (
                      <div>
                        <Button
                          color='warning'
                          onClick={() => {
                            modalActions.showLoadingScreen(
                              dispatch,
                              modalTypeConstants.LOADING,
                              { message: loadingMessagesConstants.SEND_OFFER }
                            )
                            operatFilesService
                              .sendCSVFile(
                                state.operatFiles.csv,
                                state.operatFiles.id
                              )
                              .then(() => {
                                reloadOperat()
                                modalActions.hideLoadingScreen(dispatch)
                              })
                          }}
                        >
                          Wylij dane
                        </Button>
                        <Button
                          color='info'
                          onClick={() => {
                            operatFilesActions.addLineToCSV(dispatch)
                          }}
                        >
                          Dodaj lini
                        </Button>
                        <Button
                          color='info'
                          onClick={() => {
                            operatFilesActions.removeLineFromCSV(
                              dispatch,
                              selectedRows
                            )
                            setSelectedRows([])
                          }}
                        >
                          Usu zaznaczone linie
                        </Button>
                        <ExportCSVButton {...props.csvProps}>
                          Export do pliku
                        </ExportCSVButton>
                        <Button onClick={() => inputFile.current.click()}>
                          {csvToRead
                            ? 'wybierz plik [' + csvToRead.name + ']'
                            : 'wybierz plik'}
                        </Button>
                        <input
                          type='file'
                          id='file'
                          ref={inputFile}
                          style={{ display: 'none' }}
                          accept='.csv'
                          onChange={(e) => {
                            setCsvToRead(e.target.files[0])
                          }}
                        />
                        <Button
                          disabled={!csvToRead}
                          onClick={(e) => {
                            e.preventDefault()
                            submit()
                          }}
                        >
                          Importuj
                        </Button>
                        <Button
                          color='secondary'
                          id='copyInputArea'
                          onClick={() => {
                            modalActions.showLoadingScreen(
                              dispatch,
                              modalTypeConstants.OPERATS_LIST_MODAL,
                              { operat: operat, dataType: 'csv' }
                            )
                          }}
                          style={{ marginBottom: '1rem' }}
                        >
                          kopiuj dane transakcyjne z innego operatu
                        </Button>
                        <hr />
                        <BootstrapTable
                          {...props.baseProps}
                          selectRow={selectRow()}
                          tabIndexCell
                          bootstrap4
                          striped
                          hover
                          condensed
                          bordered={false}
                          cellEdit={cellEdit}
                        />
                      </div>
                    )}
                  </ToolkitProvider>
                </Card>
              </Col>
            </Row>
          </div>
        </CardBody>
      </Card>
    </>
  )
}

export default CsvComponent
