import { modalActions } from 'actions'
import { modalTypeConstants, loadingMessagesConstants } from '../../constants'
import { operatService } from 'services'
import { REMOVE_OPERAT, SET_OPERATS } from './constants'

export const getOperats = (dispatch, location) => {
  let params = {}
  const search = new URLSearchParams(location.search)
  if (search.get('pageNumber')) {
    const pageNumber = search.get('pageNumber')
    params = { ...params, pageNumber }
  }
  modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.LOADING_DATA })
  operatService
    .getOperatByOperator(params)
    .then((result) => {
      if (result.length > 0) {
        dispatch({
          type: SET_OPERATS,
          payload: result
        })
        setTimeout(() => {
          modalActions.hideLoadingScreen(dispatch)
        }, 100)
      } else {
        modalActions.showLoadingScreen(
          dispatch,
          modalTypeConstants.SHOW_NO_DATA,
          {}
        )
      }
    })
}

export const removeOperat = (dispatch, id) => {
  dispatch({
    type: REMOVE_OPERAT,
    payload: id
  })
}
