import React, { useState, useRef, useContext } from 'react'
import NotificationAlert from 'react-notification-alert'
import { useHistory } from 'react-router-dom'
import { v4 as uuidv4 } from 'uuid'
import { AppContext } from '../../contexts/context'
import { operatService } from '../../services'
import { operatFilesActions, modalActions } from '../../actions'
import { modalTypeConstants, loadingMessagesConstants } from '../../constants'

import UserInfo from 'views/Common/UserInfo'

import {
  Button,
  Card,
  CardBody,
  Collapse,
  Input
} from 'reactstrap'

function PaidOrder (props) {
  const [state, dispatch] = useContext(AppContext)
  const [operatType, setOperatType] = useState('')
  const [userInfo, setUserInfo] = useState(false)
  const history = useHistory()
  const options = (row) => {
    return {
      place: 'tc',
      message: (
        <div>
          <div>
            Twj operat: {row.description} zosta utworzony, przejdz do historii
            operatw
          </div>
        </div>
      ),
      type: 'info',
      icon: 'nc-icon nc-atom',
      autoDismiss: 5
    }
  }
  const notifyRegister = useRef(null)

  const startNotify = (row) => {
    notifyRegister.current.notificationAlert(options(row))
    setTimeout(function () {
      operatFilesActions.generateNew(
        dispatch,
        `Utworzono operat: ${row.description}`,
        uuidv4()
      )
    }, 5000)
  }

  const createOperat = (row) => {
    const timeElapsed = Date.now()
    const today = new Date(timeElapsed)
    const body = {
      name: `${row.name + ', ' + operatType.name + ' : ' + today.toLocaleString()}`,
      orderId: row.id,
      operatorId: row.operators[0],
      operatTypeId: operatType.id
    }

    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { body, message: loadingMessagesConstants.SAVE_OPERAT })
    operatService.createOperat(body).then(() => {
      modalActions.hideLoadingScreen(dispatch)
      startNotify(row)
      setOperatType('')
      history.push('/history')
    })
  }

  const dropDown = () => {
    return (
      <div>
        <Input
          type='select' className='dark-select' id='operat-type-select' value={operatType.id ? operatType.id : ''} onChange={(e) => {
            if (e.target.value === 'CLEAR') {
              setOperatType('')
            } else {
              setOperatType(state.marketPage.operatTypes.find(x => x.id === e.target.value))
            }
          }} style={{ width: '100%' }} placeholder='wybierz typ operatu'
        >
          <option value='' disabled>Wybierz rodzaj operatu</option>
          {state.marketPage.operatTypes.map((element) => {
            return (
              <option key={element.id} value={element.id}>{element.name}</option>
            )
          })}
          <option value='CLEAR'>---Kasuj wybr---</option>
        </Input>
      </div>
    )
  }

  return (
    <div>
      <NotificationAlert ref={notifyRegister} />
      <div>
        {dropDown()}
        <div className='d-flex justify-content-between'>
          <Button
            style={{ width: '50%' }}
            color='primary'
            onClick={() => createOperat(state.marketPage.selectedOrder)}
            disabled={operatType === ''}
          >
            Stwrz operat
          </Button>
          <Button
            style={{ width: '50%' }}
            color='info'
            onClick={() => setUserInfo(!userInfo)}
          >
            informacje o zleceniodawcy
          </Button>
        </div>
        <Collapse isOpen={userInfo} style={{ marginTop: '1rem' }}>
          <Card>
            <CardBody>
              {userInfo && <UserInfo id={state.marketPage.selectedOrder.clientId} />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
    </div>
  )
}

export default PaidOrder
