import React, { useContext, useEffect, useRef, useState } from 'react'
import { useTranslation } from 'react-i18next'
import NotificationAlert from 'react-notification-alert'
import { useLocation } from 'react-router-dom'
import {
  Button, Card,
  CardBody, Collapse
} from 'reactstrap'
import { v4 as uuidv4 } from 'uuid'
import { modalActions, operatFilesActions } from '../../actions/index'
import { loadingMessagesConstants, modalTypeConstants, userRolesConstants } from '../../constants'
import { AppContext } from '../../contexts/context'
import { operatService, operatTypeService } from '../../services'
import CsvComponent from './CsvComponent'
import { InfoForAdmin } from './InfoForAdmin'
import InputArea from './InputArea'
import PhotoArea from './PhotoArea'

function Create (props) {
  const [state, dispatch] = useContext(AppContext)
  const [operat, setOperat] = useState({})
  const [operatTypeParams, setOperatTypeParams] = useState([])
  const { t } = useTranslation('common')

  const location = useLocation()
  useEffect(() => {
    setOperat(location.state.row)
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.LOADING_DATA })
    operatFilesActions.getFormatedFilesByOperat(dispatch, location.state.row).then((operatTypeId) => {
      operatTypeService
        .getTypeParams(operatTypeId)
        .then((res) => {
          setOperatTypeParams(res)
          if (Array.isArray(res)) {
            res = { input: [], photo: [] }
            operatFilesActions.loadOperatType(dispatch, res)
          }
          modalActions.hideLoadingScreen(dispatch)
        })
    })

    return function cleanup () {
      operatFilesActions.clearOperatState(dispatch)
    }
  }, [dispatch, location.state.row])

  const reloadOperat = () => {
    operatFilesActions.getFormatedFilesByOperat(dispatch, operat)
  }
  const options = {
    place: 'tc',
    message: (
      <div>
        <div>
          {t('create.notification.message')}
        </div>
      </div>
    ),
    type: 'info',
    icon: 'nc-icon nc-atom',
    autoDismiss: 5
  }

  useState(false)
  const notifyRegister = useRef(null)
  const startNotify = () => {
    modalActions.showLoadingScreen(
      dispatch,
      modalTypeConstants.LOADING,
      { message: loadingMessagesConstants.GENERATE_OPERAT }
    )
    operatService.generateOperat(state.operatFiles.id).then(
      () => modalActions.hideLoadingScreen(dispatch)).catch((error) => {
      modalActions.showLoadingScreen(dispatch, modalTypeConstants.ERROR_MESSAGE, { message: error })
    })
    notifyRegister.current.notificationAlert(options)
    if (state.operatFiles.input.length > 0) {
      setTimeout(function () {
        operatFilesActions.generateNew(
          dispatch,
          state.operatFiles.input[0].data[0].value,
          uuidv4()
        )
      }, 5000)
    }
  }

  const [inputArea, setInputArea] = useState(false)
  const [csvArea, setCsvArea] = useState(false)
  const [photoArea, setPhotoArea] = useState(false)
  const isAdmin = state.user.userType === userRolesConstants.ADMIN

  const isAllFilled = state.operatFiles.files.csv ? state.operatFiles.files.input.item1 && state.operatFiles.files.csv.item1 && (state.operatFiles.files.photo.length === 10 || (state.operatFiles.photo.data && state.operatFiles.photo.data.every(x => x.data.every(t => t.hasOwnProperty('url') && !t.hasOwnProperty('file'))))) : false

  return (
    <div className='content'>
      {isAdmin && <InfoForAdmin />}
      <NotificationAlert ref={notifyRegister} />
      <Button
        style={{ width: '16rem' }}
        color='primary'
        onClick={() => startNotify()}
        disabled={!isAllFilled}
      >
        {t('create.buttons.generate')}
      </Button>
      <br />
      <div>
        <Button
          color='primary'
          onClick={() => setInputArea(!inputArea)}
          style={{ marginBottom: '1rem', width: '16rem' }}
        >
          {t('create.buttons.description')}
        </Button>
        <Collapse isOpen={inputArea}>
          <Card>
            <CardBody>
              {inputArea && <InputArea reloadOperat={reloadOperat} operatTypeParams={operatTypeParams} operat={operat} />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
      <div>
        <Button
          color='primary'
          onClick={() => setCsvArea(!csvArea)}
          style={{ marginBottom: '1rem', width: '16rem' }}
        >
          {t('create.buttons.csvFiles')}
        </Button>
        <Collapse isOpen={csvArea}>{csvArea && <CsvComponent reloadOperat={reloadOperat} operat={operat} />}</Collapse>
      </div>
      <div>
        <Button
          disabled={operatTypeParams.length === 0}
          color='primary'
          onClick={() => setPhotoArea(!photoArea)}
          style={{ marginBottom: '1rem', width: '16rem' }}
        >
          {t('create.buttons.photos')}
        </Button>
        <Collapse isOpen={photoArea}>
          {photoArea && <PhotoArea operatTypeParams={operatTypeParams} operat={operat} />}
        </Collapse>
      </div>
    </div>
  )
}

export default Create
