import { operatFiles, modal, operator } from '../resources/index'
import React, { useReducer, createContext, useMemo } from 'react'
import rootReducer from '../reducers/root.reducer'
export const AppContext = createContext()

const initState = {
  operatFiles: operatFiles,
  operator: operator,
  modal: modal,
  user: { userType: '' }
}

export const AppContextProvider = ({ children }) => {
  const [state, dispatch] = useReducer(rootReducer, initState)
  // Important(!): memoize array value. Else all context consumers update on *every* render
  const store = useMemo(() => [state, dispatch], [state])
  return (
    <AppContext.Provider value={store}> {children} </AppContext.Provider>
  )
}
