import React, { useEffect } from 'react'
import { useTranslation } from 'react-i18next'

import { Route, Switch, Redirect, useHistory } from 'react-router-dom'

import {
  MsalProvider,
  AuthenticatedTemplate,
  UnauthenticatedTemplate, useIsAuthenticated,
  useMsal
} from '@azure/msal-react'
import { CustomNavigationClient } from './utils/NavigationClient'
import { EventType, InteractionType } from '@azure/msal-browser'
import { b2cPolicies } from './authConfig'

import 'bootstrap/dist/css/bootstrap.css'
import 'assets/scss/paper-dashboard.scss?v=1.2.0'
import 'assets/demo/demo.css'
import 'assets/css/styles.css'
import 'perfect-scrollbar/css/perfect-scrollbar.css'

import GeneralLayout from './layouts/GeneralLayout'
import Login from './views/Login'
import LoadingModal from './views/Modal/LoadingModal'

import { AppContextProvider } from './contexts/context'

function App ({ pca }) {
  // The next 3 lines are optional. This is how you configure MSAL to take advantage of the router's navigate functions when MSAL redirects between pages in your app
  const history = useHistory()
  const navigationClient = new CustomNavigationClient(history)
  pca.setNavigationClient(navigationClient)

  return (
    <MsalProvider instance={pca}>
      <MainContent />
    </MsalProvider>
  )
}

const MainContent = (props) => {
  const { instance } = useMsal()

  /**
   * Using the event API, you can register an event callback that will do something when an event is emitted.
   * When registering an event callback in a react component you will need to make sure you do 2 things.
   * 1) The callback is registered only once
   * 2) The callback is unregistered before the component unmounts.
   * For more, visit: https://github.com/AzureAD/microsoft-authentication-library-for-js/blob/dev/lib/msal-react/docs/events.md
   */
  useEffect(() => {
    const callbackId = instance.addEventCallback((event) => {
      if (event.eventType === EventType.LOGIN_FAILURE) {
        if (
          event.error &&
          event.error.errorMessage.indexOf('AADB2C90118') > -1
        ) {
          if (event.interactionType === InteractionType.Redirect) {
            instance.loginRedirect(b2cPolicies.authorities.forgotPassword)
          } else if (event.interactionType === InteractionType.Popup) {
            instance
              .loginPopup(b2cPolicies.authorities.forgotPassword)

              .catch((e) => {

              })
          }
        }
        if (
          event.error &&
          event.error.errorMessage.indexOf('AADB2C90091') > -1
        ) {
          if (event.interactionType === InteractionType.Redirect) {
            instance.loginRedirect(b2cPolicies.authorities.signUpSignIn)
          } else if (event.interactionType === InteractionType.Popup) {
            instance
              .loginPopup(b2cPolicies.authorities.signUpSignIn)
              .catch((e) => {

              })
          }
        }
      }

      if (event.eventType === EventType.LOGIN_SUCCESS) {
        if (event?.payload) {
          /**
           * We need to reject id tokens that were not issued with the default sign-in policy.
           * "acr" claim in the token tells us what policy is used (NOTE: for new policies (v2.0), use "tfp" instead of "acr").
           * To learn more about B2C tokens, visit https://docs.microsoft.com/en-us/azure/active-directory-b2c/tokens-overview
           */
          if (
            event.payload.idTokenClaims.tfp ===
            b2cPolicies.names.forgotPassword
          ) {
            window.alert(
              'Password has been reset successfully. \nPlease sign-in with your new password'
            )
            return instance.logout()
          }
        }
      }
    })

    return () => {
      if (callbackId) {
        instance.removeEventCallback(callbackId)
      }
    }
  }, [instance])

  return (
    <div className='App'>
      <AuthenticatedTemplate>
        <Pages />
      </AuthenticatedTemplate>

      <UnauthenticatedTemplate>
        <Login />
      </UnauthenticatedTemplate>
    </div>
  )
}

function Pages (props) {
  const { instance } = useMsal()
  const isAuthenticated = useIsAuthenticated()
  const { t } = useTranslation('common')

  const activeAccount = () => {
    const acc = instance.getActiveAccount()
    return acc
  }
  return (
    <AppContextProvider>
      <Switch>
        <Route
          exact
          path='/'
          render={(props) => <AoperatWaiting {...props} />}
        />
        {isAuthenticated && (
          <Redirect
            strict
            from='/start'
            to={
            activeAccount() !== null
              ? '/market'
              : '/'
          }
          />
        )}
        <Route path='/' render={(props) => <GeneralLayout t={t} {...props} account={activeAccount()} />} />
      </Switch>
      <LoadingModal />
    </AppContextProvider>
  )
}

function AoperatWaiting (props) {
  const reloadAfterLogin = () => {
    props.history.location.pathname === '/' &&
      setTimeout(() => {
        props.history.push('/start')
        window.location.reload()
      }, 500)
  }
  return <div>{reloadAfterLogin()}</div>
}

export default App
