import { modalConstants } from '../constants'

export const modalActions = {
  showLoadingScreen,
  hideLoadingScreen
}

function showLoadingScreen (dispatch, type, data) {
  dispatch({
    type: modalConstants.MODAL_SHOW_LOADING_SCREEN,
    payload: { showModal: true, type, data }
  })
}

function hideLoadingScreen (dispatch) {
  dispatch({
    type: modalConstants.MODAL_HIDE_LOADING_SCREEN
  })
}
