import React, { useCallback, useContext, useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import {
  Button, Card,
  CardBody, Col, Row, UncontrolledCollapse
} from 'reactstrap'
import { modalActions, operatFilesActions } from '../../actions'
import { loadingMessagesConstants, modalTypeConstants } from '../../constants'
import { AppContext } from '../../contexts/context'
import { operatFilesService } from '../../services'
import ImageDropzone from '../Common/ImageDropzone'
import ReactstrapImageGallery from './ReactstrapImageGallery'

function PhotoArea (props) {
  const [state, dispatch] = useContext(AppContext)
  const { t } = useTranslation('common')
  const [disabledBtns, setDisableBtns] = useState([])
  const mapPhotos = useCallback((photo) => {
    const result = props.operatTypeParams.photo
    for (let i = 0; i < result.length; i++) {
      result[i].data = result[i].data.map(x => {
        const finded = photo.find(p => p.item1 === x.title)
        if (!finded) {
          return x
        } else {
          return { ...x, url: finded.item2 }
        }
      })
    }
    return { data: result }
  }, [props.operatTypeParams])

  useEffect(() => {
    const photo = state.operatFiles.files.photo
    !!photo && photo.length > 0
      ? operatFilesActions.loadOperatEmptyPhoto(dispatch, mapPhotos(photo))
      : operatFilesActions.loadOperatEmptyPhoto(
        dispatch,
        { data: props.operatTypeParams.photo }
      )
  }, [dispatch, state.operatFiles.files, props.operatTypeParams, mapPhotos])

  const handleSendPhotos = (id) => {
    setDisableBtns(prev => [...prev, id])
    modalActions.showLoadingScreen(
      dispatch,
      modalTypeConstants.LOADING,
      { message: loadingMessagesConstants.SEND_PHOTOS }
    )
    operatFilesService.sendPICTUREFile(
      state.operatFiles.photo.data[id].data,
      state.operatFiles.id
    ).then(() => {
      operatFilesActions.removePhotoFiles(dispatch, id)
      modalActions.hideLoadingScreen(dispatch)
    })
  }

  const isSendPhotosButtonDisabled = (id) => {
    return !state.operatFiles.photo.data[id].data.every((item) =>
      item.hasOwnProperty('url') || item.hasOwnProperty('file')
    ) ||
      !state.operatFiles.photo.data[id].data.some(x => x.hasOwnProperty('file')) ||
      disabledBtns.includes(id)
  }

  const handlePhotosDelete = async (id) => {
    setDisableBtns(prev => [...prev, id])
    modalActions.showLoadingScreen(
      dispatch,
      modalTypeConstants.LOADING,
      { message: loadingMessagesConstants.DELETE_FILE }
    )

    state.operatFiles.photo.data[id].data.map(async x => await operatFilesService.removeSingleFile(state.operatFiles.id, x.title).catch(err => console.log(err)))
    operatFilesActions.removePhotos(dispatch, id)
    modalActions.hideLoadingScreen(dispatch)
  }
  return (
    <Card>
      <CardBody>
        <Button
          color='secondary'
          id='copyInputArea'
          onClick={() => {
            modalActions.showLoadingScreen(
              dispatch,
              modalTypeConstants.OPERATS_LIST_MODAL,
              { operat: props.operat, history: props.history, dataType: 'photos' }
            )
          }}
          style={{ marginBottom: '1rem' }}
        >
          kopiuj zdjcia z innego operatu
        </Button>
        {!!state.operatFiles.photo.data &&
          state.operatFiles.photo.data.map((item, id) => {
            return (
              <div key={item.name + 'Area' + id}>
                <Card style={{ boxShadow: '6px 6px 10px 2px rgb(0 0 0 / 15%)' }}>
                  <CardBody>
                    <Button
                      color='info'
                      id={item.name + 'Area' + id}
                      style={{ marginBottom: '1rem', width: '14rem' }}
                    >
                      {t(item.text)}
                    </Button>
                    <Button
                      color='success'
                      id={item.name + 'send' + id}
                      style={{ marginBottom: '1rem', width: '14rem' }}
                      onClick={() => { handleSendPhotos(id) }}
                      disabled={isSendPhotosButtonDisabled(id)}
                    >
                      {`wylij ${item.text}`}
                    </Button>
                    <Button
                      color='danger'
                      id={item.name + 'send' + id}
                      style={{ marginBottom: '1rem', width: '14rem' }}
                      onClick={() => {
                        handlePhotosDelete(id)
                      }}
                      disabled={
                    !state.operatFiles.photo.data[id].data.every((item) =>
                      item.hasOwnProperty('url')
                    )
                  }
                    >
                      {`usu ${item.text}`}
                    </Button>
                    <UncontrolledCollapse toggler={item.name + 'Area' + id}>
                      <br />
                      <Row>
                        <Col sm='12' md={{ size: 6, offset: 3 }}>
                          <ImageDropzone id={id} multiple enableBtn={() => setDisableBtns(prev => prev.filter(x => x !== id))} fileNames={item.data.map(x => x.title)} />
                        </Col>
                      </Row>
                      <br />
                      <ReactstrapImageGallery images={item.data} limit={4} />
                    </UncontrolledCollapse>
                  </CardBody>
                </Card>
              </div>
            )
          })}
      </CardBody>
    </Card>
  )
}

export default PhotoArea
