import React, { useState, useContext } from 'react'
import PropositionList from './PropositionList'
import UserInfo from 'views/Common/UserInfo'
import { Card, Button, Collapse, CardBody } from 'reactstrap'
import { orderService } from 'services'
import { AppContext } from '../../contexts/context'
import { userRolesConstants } from '../../constants'
import { useMsal } from '@azure/msal-react'
import { updateOrder } from './actions'
function ReservedOrder (props) {
  const { accounts } = useMsal()
  const [userInfo, setUserInfo] = useState(false)
  const [sendedOffers, setSendedOffers] = useState(false)
  const [state, dispatch] = useContext(AppContext)
  const isAdmin = () => { return state.user.userType === userRolesConstants.ADMIN }

  return (
    <div>
      {isAdmin() && (
        <Button
          color='primary'
          className='long-btn'
          id='payButton'
          style={{ marginBottom: '1rem' }}
          onClick={() => {
            orderService.payForOrder(state.marketPage.selectedOrder.id).then(() => {
              updateOrder(dispatch, state.marketPage.selectedOrder.id)
            }
            )
          }}
        >
          zapa
        </Button>
      )}
      <div>
        <Button
          color='secondary'
          className='long-btn proposition-list-toggler'
          onClick={() => setSendedOffers(!sendedOffers)}
          style={{ marginBottom: '1rem' }}
        >
          wysane oferty
        </Button>
        <Collapse isOpen={sendedOffers}>
          <Card>
            <CardBody>
              {sendedOffers && <PropositionList />}
            </CardBody>
          </Card>
        </Collapse>
      </div>
      {accounts[0].idTokenClaims.oid !== state.marketPage.selectedOrder.clientId &&
        <div>
          <Button
            color='info'
            className='long-btn'
            onClick={() => setUserInfo(!userInfo)}
            style={{ marginBottom: '1rem' }}
          >
            informacje o zleceniodawcy
          </Button>
          <Collapse isOpen={userInfo}>
            <Card>
              <CardBody>
                {userInfo && <UserInfo id={state.marketPage.selectedOrder.clientId} />}
              </CardBody>
            </Card>
          </Collapse>
        </div>}
    </div>
  )
}

export default ReservedOrder
