import { operatService, operatTypeService, orderService, orderProposition } from 'services'
import { modalActions } from 'actions'
import { modalTypeConstants, loadingMessagesConstants, userRolesConstants } from '../../constants'
import { SET_OPERAT_TYPES, SET_ORDERS, SET_SELECTED_ORDER, ADD_ORDER, REMOVE_ORDER, REPLACE_ORDER, SET_PROPOSITIONS, ADD_PROPOSITION, REPLACE_PROPOSITION } from './constants'
import confirm from 'reactstrap-confirm'

export const addReview = async (event, rating, order, operatId, dispatch) => {
  event.preventDefault()
  const comment = event.target.elements['review-comment'].value
  modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.ADD_REVIEW })
  const review = {
    comment,
    rating,
    operatorId: order.operators[0],
    clientId: order.clientId,
    operatId
  }
  operatService.postReview(review).catch(err => console.log(err)).finally(() => modalActions.hideLoadingScreen(dispatch))
}

export const selectOrder = (dispatch, order) => {
  dispatch({
    type: SET_PROPOSITIONS,
    payload: []
  })
  dispatch({
    type: SET_SELECTED_ORDER,
    payload: order
  })
}

export const appendFiltersToUrl = (event, history) => {
  event.preventDefault()
  const locationPostCode = event.target.value
  const search = new URLSearchParams(history.location.search)
  search.delete('pageNumber')
  if (locationPostCode === '') {
    search.delete('locationPostCode')
  } else {
    search.set('locationPostCode', locationPostCode)
  }
  history.push({
    pathname: '/market',
    search: search.toString()
  })
}

export const getOrders = (dispatch, userType, location, setExpanded) => {
  let params = {}
  const search = new URLSearchParams(location.search)
  if (search.get('locationPostCode')) {
    const locationPostCode = search.get('locationPostCode')
    params = { ...params, locationPostCode }
  }
  if (search.get('pageNumber')) {
    const pageNumber = search.get('pageNumber')
    params = { ...params, pageNumber }
  }
  modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.LOADING_DATA })
  const handleResponse = (result) => {
    dispatch({
      type: SET_ORDERS,
      payload: result
    })
    if (result.length > 0) {
      operatTypeService.getAllTypes().then((res) => {
        modalActions.hideLoadingScreen(dispatch)
        dispatch({
          type: SET_OPERAT_TYPES,
          payload: res
        })
        const params = new URLSearchParams(location.search)
        const id = params.get('id')
        if (id) {
          setExpanded(id)
        }
      })
    } else {
      modalActions.showLoadingScreen(
        dispatch,
        modalTypeConstants.SHOW_NO_DATA,
        {}
      )
    }
  }
  switch (userType) {
    case 'admin': orderService
      .getOrders(params)
      .then((result) => {
        handleResponse(result)
      }).finally(() => modalActions.hideLoadingScreen(dispatch))
      break
    case 'operator': orderService
      .getOrdersFromMarket(params)
      .then((result) => {
        handleResponse(result)
      }).finally(() => modalActions.hideLoadingScreen(dispatch))
      break
    case 'client': orderService
      .getMyOrders(params)
      .then((result) => {
        handleResponse(result)
      }).finally(() => modalActions.hideLoadingScreen(dispatch))
      break
    default:
  }
}

export const addOrder = (dispatch, order) => {
  modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { order, message: loadingMessagesConstants.SAVE_OPERAT })
  orderService.postOrder(order).then((response) => {
    dispatch({
      type: ADD_ORDER,
      payload: response
    })
  }).finally(() => modalActions.hideLoadingScreen(dispatch))
}

export const deleteOrder = async (dispatch, id, t) => {
  const result = await confirm({
    title: <>{t('market.table.confirm.warning')}</>,
    message: t('market.table.confirm.message'),
    confirmText: t('market.table.confirm.delete'),
    cancelText: t('market.table.confirm.back'),
    confirmColor: 'danger',
    cancelColor: 'link text-primary'
  })
  if (result) {
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.DELETE_ORDER })
    orderService.deleteOrder(id).then((deleted) => {
      if (deleted) {
        dispatch({
          type: REMOVE_ORDER,
          payload: id
        })
      }
    }).finally(() => modalActions.hideLoadingScreen(dispatch))
  }
}

export const updateOrder = (dispatch, id) => {
  orderService.getSingleOrder(id).then(res => {
    dispatch({
      type: REPLACE_ORDER,
      payload: res
    })
  })
}

export const getPropositions = (dispatch, order, userId, userType) => {
  modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.LOADING_DATA })
  orderProposition.getPropositionById(order.id).then((result) => {
    let payload
    if (userType === userRolesConstants.ADMIN) {
      payload = result
    } else if (order.clientId !== userId) {
      payload = result.filter(x => x.operatorId === userId)
    } else {
      payload = result
    }
    dispatch({
      type: SET_PROPOSITIONS,
      payload
    })
  }).finally(() => modalActions.hideLoadingScreen(dispatch))
}

export const addProposition = (dispatch, proposition) => {
  orderProposition.sendProposition(proposition).then((res) => {
    dispatch({
      type: ADD_PROPOSITION,
      payload: res
    })
  }).finally(() => modalActions.hideLoadingScreen(dispatch))
}

export const reserveOrderAndProposition = (dispatch, propositionId, orderId) => {
  orderProposition.reserveOrderAndProposition(propositionId).then(result => {
    updateOrder(dispatch, orderId)
    dispatch({
      type: REPLACE_PROPOSITION,
      payload: result
    })
  })
}

export const unreserveProposition = (dispatch, propositionId, orderId) => {
  orderProposition.unReserveProposition(propositionId).then(result => {
    updateOrder(dispatch, orderId)
    dispatch({
      type: REPLACE_PROPOSITION,
      payload: result
    })
  })
}
