import { useContext, useEffect, useState } from 'react'
import { adminService, operatorService, userService } from '../../services'
import BootstrapTable from 'react-bootstrap-table-next'
import {
  Row,
  Col,
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  Button
} from 'reactstrap'
import { userRolesConstants, modalTypeConstants, loadingMessagesConstants } from '../../constants'
import { modalActions } from 'actions'
import { AppContext } from 'contexts/context'
import { useHistory } from 'react-router'
import Pagination from '@mui/material/Pagination'

import confirm from 'reactstrap-confirm'
function Users (props) {
  const [, dispatch] = useContext(AppContext)
  const history = useHistory()
  const [clients, setClients] = useState([])
  const [operators, setOperators] = useState([])
  const [admins, setAdmins] = useState([])
  const [isDropdownOpen, setIsDropdownOpen] = useState(false)
  const [userType, setUserType] = useState(userRolesConstants.CLIENT)
  const userTypes = [
    { label: 'Klient', value: 'client' },
    { label: 'Operator', value: 'operator' },
    { label: 'Administrator', value: 'admin' }
  ]
  const handlePromoteUser = (response) => {
    if (response === 400) {
      modalActions.showLoadingScreen(dispatch, modalTypeConstants.PROMOTE_USER_ERROR)
    } else {
      modalActions.hideLoadingScreen(dispatch)
    }
  }
  const promoteUser = async (id, name) => {
    const result = await confirm({
      title: <>Ostrzeenie</>,
      message: `Czy na pewno chcesz wypromowa uytkownika ${name}`,
      confirmText: 'Tak',
      confirmColor: 'info',
      cancelColor: 'link text-primary',
      cancelText: 'nie'
    })
    if (!result) {
      return
    }
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: 'Trwa promocja uytkownika' })
    switch (userType) {
      case userRolesConstants.CLIENT: {
        await operatorService.promote(id).then(handlePromoteUser)
        setClients(clients.filter(x => x.id !== id).concat(clients.filter(x => x.id === id).map(x => ({ ...x, canPromote: false }))))
        break
      }
      case userRolesConstants.OPERATOR: {
        await adminService.promote(id).then(handlePromoteUser)
        setOperators(operators.filter(x => x.id !== id).concat(operators.filter(x => x.id === id).map(x => ({ ...x, canPromote: false }))))
        break
      }
      default:
    }
  }
  const handleChange = (event, newPage) => {
    const search = new URLSearchParams(history.location.search)
    search.set('pageNumber', newPage.toString())
    history.push({
      pathname: '/users-promotion',
      search: search.toString()
    })
  }

  const columns = () => {
    return [
      {
        dataField: 'id',
        text: 'ID',
        hidden: false
      },
      {
        dataField: 'name',
        text: 'Nazwa',
        editable: false,
        align: 'center',
        headerAlign: 'center'
      },
      {
        dataField: 'mail',
        text: 'Adres e-mail',
        editable: false,
        align: 'center',
        headerAlign: 'center'
      },
      userType !== userRolesConstants.CLIENT &&
      {
        text: 'Profil uytkownika',
        align: 'center',
        headerAlign: 'center',
        formatter: (cellContent, row) => <Button color='primary' onClick={() => history.push({ pathname: '/users-profile', search: '?operatorId=' + row.id })}>
          Profil uytkownika
        </Button>
      },
      {
        text: 'Promocja',
        align: 'center',
        headerAlign: 'center',
        formatter: (cellContent, row) => <Button disabled={!row.canPromote} color='success' onClick={() => promoteUser(row.id, row.name)}>
          Promocja
        </Button>
      }
    ]
  }

  const loadUsers = () => {
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.LOADING_DATA })
    switch (userType) {
      case userRolesConstants.CLIENT: loadClients(); break
      case userRolesConstants.OPERATOR: loadOperators(); break
      case userRolesConstants.ADMIN: loadAdmins(); break
      default:
    }
  }

  const createSearchParams = () => {
    let params = {}
    const search = new URLSearchParams(history.location.search)
    if (search.get('pageNumber')) {
      const pageNumber = search.get('pageNumber')
      params = { ...params, pageNumber }
    }
    return params
  }

  const loadClients = () => {
    userService.getClients(createSearchParams()).then((response) => {
      setClients(response)
      modalActions.hideLoadingScreen(dispatch)
    })
  }
  const loadOperators = () => {
    userService.getOperators(createSearchParams()).then((response) => {
      setOperators(response)
      modalActions.hideLoadingScreen(dispatch)
    })
  }
  const loadAdmins = () => {
    userService.getAdmins(createSearchParams()).then((response) => {
      setAdmins(response)
      modalActions.hideLoadingScreen(dispatch)
    })
  }

  useEffect(() => {
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.LOADING_DATA })
    userService.getClients(createSearchParams()).then((response) => {
      setClients(response)
      modalActions.hideLoadingScreen(dispatch)
    })
  // eslint-disable-next-line
  }, [dispatch])
  useEffect(() => {
    loadUsers()
    // eslint-disable-next-line
  }, [history.location, userType])

  const getTableData = () => {
    switch (userType) {
      case userRolesConstants.CLIENT: return clients
      case userRolesConstants.OPERATOR: return operators
      case userRolesConstants.ADMIN: return admins
      default: return []
    }
  }
  return (
    <div className='content'>
      <Dropdown isOpen={isDropdownOpen} toggle={() => setIsDropdownOpen(!isDropdownOpen)}>
        <Pagination count={localStorage.getItem('pageCount')} color='primary' page={(new URLSearchParams(history.location.search)).get('pageNumber') ? parseInt((new URLSearchParams(history.location.search)).get('pageNumber')) : 1} onChange={handleChange} />
        <DropdownToggle caret>Wybierz rodzaj uytkownika [{userTypes.map(x => x.value === userType ? x.label : '')}]</DropdownToggle>
        <DropdownMenu>
          {userTypes.map((element) => {
            return (
              <DropdownItem
                key={element.value}
                onClick={() => {
                  setUserType(element.value)
                }}
              >
                {element.label}
              </DropdownItem>
            )
          })}
        </DropdownMenu>
      </Dropdown>
      <Row>
        <Col md='12'>
          <BootstrapTable
            bootstrap4
            keyField='id'
            data={getTableData()}
            columns={columns()}
            striped
            hover
            condensed
            bordered={false}
          />
        </Col>
      </Row>
    </div>
  )
}
export default Users
