import React, { useContext, useRef, useEffect } from 'react'
import NotificationAlert from 'react-notification-alert'
import { v4 as uuidv4 } from 'uuid'

import {
  UncontrolledTooltip,
  Col,
  FormGroup,
  Input,
  Button,
  UncontrolledCollapse
} from 'reactstrap'
import { operatFilesService } from '../../services'
import { modalTypeConstants, loadingMessagesConstants } from '../../constants'
import { operatFilesActions, modalActions } from '../../actions'
import { AppContext } from '../../contexts/context'

function InputArea (props) {
  const [state, dispatch] = useContext(AppContext)

  useEffect(() => {
    const load = () => {
      modalActions.showLoadingScreen(
        dispatch,
        modalTypeConstants.LOADING,
        {}
      )
      operatFilesActions.loadOperatInput(dispatch, dataJson.item2)
    }

    const dataJson = state.operatFiles.files.input
    !!dataJson && (Object.keys(dataJson).length > 0 ? load() : operatFilesActions.loadOperatEmptyInput(dispatch, props.operatTypeParams.input))
  }, [
    state.operatFiles.operatTypeId,
    dispatch,
    state.operatFiles.files,
    props.operatTypeParams
  ])

  const handleChangeValue = (e, id, idx) => {
    operatFilesActions.updateValue(dispatch, e, id, idx)
  }

  const options = {
    place: 'tc',
    message: (
      <div>
        <div>Twje dane zostay pomylnie wysane na serwer</div>
      </div>
    ),
    type: 'info',
    icon: 'nc-icon nc-atom',
    autoDismiss: 5
  }

  const notifyRegister = useRef(null)

  const startNotify = () => {
    notifyRegister.current.notificationAlert(options)
    setTimeout(function () {
      operatFilesActions.generateNew(
        dispatch,
        state.operatFiles.input[0].data[0].value,
        uuidv4()
      )
    }, 5000)
  }

  return (
    <>
      <NotificationAlert ref={notifyRegister} />
      <Button
        color='secondary'
        id='saveInputArea'
        onClick={() => {
          modalActions.showLoadingScreen(
            dispatch,
            modalTypeConstants.LOADING,
            { message: loadingMessagesConstants.SEND_OFFER }
          )
          operatFilesService
            .sendJSONFile(state.operatFiles.input, state.operatFiles.id)
            .then(() => {
              props.reloadOperat()
              modalActions.hideLoadingScreen(dispatch)
              startNotify()
            })
        }}
        style={{ marginBottom: '1rem', width: '16rem' }}
      >
        zapisz dane opisowe
      </Button>
      <Button
        color='secondary'
        id='copyInputArea'
        onClick={() => {
          modalActions.showLoadingScreen(
            dispatch,
            modalTypeConstants.OPERATS_LIST_MODAL,
            { operat: props.operat, dataType: 'params' }
          )
        }}
        style={{ marginBottom: '1rem' }}
      >
        kopiuj dane opisowe z innego operatu
      </Button>
      {state.operatFiles.input && state.operatFiles.input.length > 0 &&
        state.operatFiles.input.map((genItem, id) => {
          return (
            <div key={genItem.name + 'Area' + id}>
              <Button
                color='info'
                id={genItem.name + 'Area' + id}
                style={{ marginBottom: '1rem', width: '16rem' }}
              >
                {genItem.text}
              </Button>
              <UncontrolledCollapse toggler={genItem.name + 'Area' + id}>
                {genItem.data.map((dataItem, idx) => {
                  return (
                    <FormGroup row key={genItem.name + idx}>
                      <Col md='3'>
                        <p>{dataItem.description}</p>
                      </Col>
                      <Col sm={8}>
                        {dataItem.type === 'ekw' && <Button href='https://przegladarka-ekw.ms.gov.pl/eukw_prz/KsiegiWieczyste/wyszukiwanieKW' target='_blank'>Elektroniczna ksiega wieczysta</Button>}
                        <div style={{ display: dataItem.type === 'ekw' ? 'flex' : 'block' }}>
                          <Input
                            type={dataItem.type}
                            name={genItem.name + dataItem.type + idx}
                            id={dataItem.name + idx}
                            placeholder={dataItem.description}
                            value={dataItem.value}
                            onChange={(e) => {
                              handleChangeValue(e, id, idx)
                            }}
                          />
                          {dataItem.type === 'ekw' && <Button onClick={() => operatFilesActions.downloadFromEkw(dispatch, dataItem.value, genItem.name)}>Pobierz z ekw</Button>}
                        </div>
                        <UncontrolledTooltip
                          placement='top'
                          target={dataItem.name + idx}
                        >
                          {dataItem.tooltip}
                        </UncontrolledTooltip>
                      </Col>
                    </FormGroup>
                  )
                })}
              </UncontrolledCollapse>
            </div>
          )
        })}
    </>
  )
}

export default InputArea
