import { userConstants } from '../constants/user.constants'
import { userService } from 'services'
export const userActions = {
  setUserType
}

function setUserType (dispatch, id) {
  userService.getUserType(id).then(result => {
    dispatch({
      type: userConstants.SET_USER_TYPE,
      payload: { userType: result }
    })
  })
}
