import { modalConstants } from '../constants'

function modal (state, action) {
  switch (action.type) {
    case modalConstants.MODAL_SHOW_LOADING_SCREEN:
      return {
        ...state,
        showModal: action.payload.showModal,
        type: action.payload.type,
        data: action.payload.data
      }
    case modalConstants.MODAL_HIDE_LOADING_SCREEN:
      return {
        ...state,
        showModal: false,
        type: '',
        data: {}
      }
    default:
      return state
  }
};

export default modal
