import React, { useContext, useEffect, useState, useRef } from 'react'
import { useTranslation } from 'react-i18next'
import { useHistory } from 'react-router'
import { Button } from 'reactstrap'
import confirm from 'reactstrap-confirm'
import { modalActions, operatFilesActions } from '../../actions'
import { loadingMessagesConstants, modalTypeConstants } from '../../constants'
import { AppContext } from '../../contexts/context'
import { operatFilesService, operatService, orderService } from '../../services'
import { getOperats, removeOperat } from './actions'
function HistoryRow (props) {
  const { t } = useTranslation('common')
  const [, dispatch] = useContext(AppContext)
  const history = useHistory()
  const [order, setOrder] = useState({})
  const [operat, setOperat] = useState(null)
  const inputFile = useRef(null)
  useEffect(() => {
    setOperat(props.row)
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.LOADING_DATA })
    orderService.getSingleOrder(props.row.orderId).then(res => {
      if (res.length === 0) {
        return
      }
      setOrder(res)
    })
    operatFilesActions.getFormatedFilesByOperat(dispatch, props.row)
  }, [dispatch, props.row])

  const deleteOperat = async () => {
    const result = await confirm({
      title: <>{t('history.table.confirm.warning')}</>,
      message: t('history.table.confirm.message'),
      confirmText: 'Usu',
      cancelText: 'Wr',
      confirmColor: 'danger',
      cancelColor: 'link text-primary'
    })
    if (result) {
      modalActions.showLoadingScreen(
        dispatch,
        modalTypeConstants.LOADING,
        { message: loadingMessagesConstants.DELETE_OPERAT }
      )
      operatService.deleteOperat(operat.id).then((resp) => {
        if (resp) {
          removeOperat(dispatch, operat.id)
          modalActions.hideLoadingScreen(dispatch)
        }
      })
    }
  }
  const handleLoadOwnGeneratedOperat = (event) => {
    modalActions.showLoadingScreen(
      dispatch,
      modalTypeConstants.LOADING,
      { message: 'Trwa wgrywanie operatu' }
    )
    operatFilesService.uploadOwnGenerated(operat.id, event.currentTarget.files[0]).then(() => getOperats(dispatch, history.location)).finally(() => modalActions.hideLoadingScreen())
  }
  const downloadOperat = () => {
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.DOWNLOAD_OPERAT })
    operatFilesService.getFilesByOperat(operat.id).then(response => {
      for (const file of response) {
        if (file.item2.includes('.pdf')) {
          modalActions.hideLoadingScreen(dispatch)
          window.open(
            file.item2, '_blank')
          return
        }
      }
      modalActions.hideLoadingScreen(dispatch)
      alert('nie znaleziono pliku pdf')
    })
  }

  const signPdf = () => {
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.SIGN_OPERAT)
  }

  const sendPdf = () => {
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: 'Trwa wysyanie operatu' })
    operatService.sendOperat(props.row.id).then(() => {
      modalActions.showLoadingScreen(dispatch, modalTypeConstants.SEND_OPERAT_CONFIRMATION, {})
    })
  }

  return (
    <div>
      <div className='d-flex justify-content-around'>
        <Button
          color='success'
          type='submit'
          onClick={() => {
            props.history.push({ pathname: '/create', state: { row: operat } })
          }}
          disabled={operat && operat.isOwnGenerated}
        >
          {t('history.table.buttons.showOperat')}
        </Button>
        <Button
          color='info' type='submit' onClick={() => {
            operatService.copyOperat(operat.id).then(result => {
              operatFilesActions.getFormatedFilesByOperat(dispatch, result)
            })
            props.history.push({ pathname: '/create', state: { row: operat } })
          }}
          disabled={operat && operat.isOwnGenerated}
        >
          {t('history.table.buttons.copy')}
        </Button>
        <Button
          color='info' type='file' onClick={() => inputFile.current.click()} disabled={order.shouldBeValidated}
        >
          Wgraj operat
        </Button>
        <input
          type='file'
          id='file-upload-own-operat'
          ref={inputFile}
          style={{ display: 'none' }}
          accept='.pdf'
          onChange={(event) => handleLoadOwnGeneratedOperat(event)}
        />
        <Button
          disabled={operat && !['generated', 'signed'].includes(operat.status.toLowerCase())}
          color='primary' onClick={() => {
            downloadOperat()
          }}
        >
          {t('history.table.buttons.download')}
        </Button>
        <Button disabled={operat && operat.status.toLowerCase() !== 'generated'} color='info' type='submit' onClick={() => { signPdf() }}>
          {t('history.table.buttons.sign')}
        </Button>
        <Button disabled={operat && operat.status.toLowerCase() !== 'signed'} color='info' type='submit' onClick={() => { sendPdf() }}>
          {t('history.table.buttons.send')}
        </Button>
        <Button color='danger' onClick={() => deleteOperat(operat.id)}>
          {t('history.table.buttons.delete')}
        </Button>
      </div>
    </div>
  )
}

export default HistoryRow
