import React, { useState, useContext } from 'react'
import { useMsal } from '@azure/msal-react'
import { certificateService } from '../../services/certificate.service'
import { modalActions } from '../../actions'
import { Button, Form, FormGroup, Label, Input } from 'reactstrap'
import { AppContext } from '../../contexts/context'
import { modalTypeConstants, loadingMessagesConstants } from '../../constants'

function NewOrderForm (props) {
  const { accounts } = useMsal()
  const [, dispatch] = useContext(AppContext)
  const [certificatePassword, setCertificatePassword] = useState('')
  const [repeatPassword, setRepeatPassword] = useState('')

  const addCertificate = (e) => {
    e.preventDefault()
    const nameLastName = accounts[0].idTokenClaims.given_name + ' ' + accounts[0].idTokenClaims.family_name
    const dto = {
      operatorId: accounts[0].idTokenClaims.oid,
      nameLastName: nameLastName,
      certificatePassword: certificatePassword,
      certificateSignDate: new Date()
    }
    modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.GENERATE_CERTIFICATE })
    certificateService.generateCertificate(dto).then(() => {
      modalActions.hideLoadingScreen(dispatch)
    }).catch((error) => modalActions.showLoadingScreen(dispatch, modalTypeConstants.ERROR_MESSAGE, { message: error }))
  }

  return (
    <div>
      <Form
        onSubmit={(e) => addCertificate(e)}
      >
        <FormGroup>
          <Label for='certificatePassword'>Haso do certyfikatu</Label>
          <Input
            type='password'
            name='certificatePassword'
            id='certificatePassword'
            value={certificatePassword}
            onChange={(e) => {
              setCertificatePassword(e.target.value)
            }}
            required
          />
        </FormGroup>
        <FormGroup>
          <Label for='certificatePassword'>Powtrz haso</Label>
          <Input
            type='password'
            name='repeatPassword'
            id='repeatPassword'
            value={repeatPassword}
            onChange={(e) => {
              setRepeatPassword(e.target.value)
            }}
            required
          />
        </FormGroup>
        <Button
          disabled={certificatePassword.length === 0 || certificatePassword !== repeatPassword}
          style={{ width: '100%' }}
          color='success'
        >
          Wygeneruj certyfikat
        </Button>
      </Form>
    </div>
  )
}

export default NewOrderForm
