import operatFiles from './operatFiles.reducer'
import operator from './operator.reducer'
import modal from './modal.reducer'
import user from './user.reducer'
import operat from './operat.reducer'
import userPage from 'views/User/reducer'
import marketPage from 'views/Market/reducer'
import historyPage from 'views/History/reducer'
function combineReducers (reducers) {
  return (state = {}, action) => {
    const newState = {}
    for (const key in reducers) {
      newState[key] = reducers[key](state[key], action)
    }
    return newState
  }
}

const rootReducer = combineReducers({
  operatFiles,
  operator,
  modal,
  user,
  operat,
  userPage,
  marketPage,
  historyPage
})

export default rootReducer
