import React, { useEffect, useState, useContext } from 'react'
import BootstrapTable from 'react-bootstrap-table-next'
import {
  Button,
  Form,
  FormGroup,
  Label,
  UncontrolledTooltip
} from 'reactstrap'
import { useHistory } from 'react-router'
import { useTranslation } from 'react-i18next'
import { useMsal } from '@azure/msal-react'
import { userRolesConstants } from '../../constants'
import { AppContext } from '../../contexts/context'
import { getPropositions, reserveOrderAndProposition, unreserveProposition } from './actions'
import ReactStars from 'react-rating-stars-component'
export const isButtonDisabled = (row, propositions, accounts, clientId, isOperator) => {
  const isAnyReserved = propositions.some(x => x.reservationStatus.toLowerCase() === 'reserved')
  const isReserved = row.reservationStatus.toLowerCase() === 'reserved'
  const oid = accounts[0].idTokenClaims.oid
  const isMineOrder = clientId === accounts[0].idTokenClaims.oid
  if (isOperator && !isReserved && row.operatorId === oid) {
    return true
  }
  if (isMineOrder) {
    return isAnyReserved ? !isReserved : isReserved
  }
  return isOperator
    ? !(isReserved && row.operatorId === oid)
    : ((isAnyReserved && !isReserved) || row.operatorId !== oid)
}
function PropositionList (props) {
  const [state, dispatch] = useContext(AppContext)
  const [isIdHidden, setIsIdHidden] = useState(true)
  const { accounts } = useMsal()
  const isAdmin = state.user.userType === userRolesConstants.ADMIN
  const isOperator = state.user.userType === userRolesConstants.OPERATOR
  const { t } = useTranslation('common')
  const history = useHistory()

  useEffect(() => {
    getPropositions(dispatch, state.marketPage.selectedOrder, accounts[0].idTokenClaims.oid, state.user.userType)
  }, [dispatch, state.marketPage.selectedOrder, accounts, state.user.userType])

  const columns = [
    {
      dataField: 'id',
      text: 'ID',
      hidden: isIdHidden
    },
    {
      dataField: 'description',
      text: t('market.table.headers.description'),
      editable: false,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'price',
      text: t('market.table.headers.price'),
      editable: false,
      align: 'center',
      headerAlign: 'center'
    },
    {
      dataField: 'reservationStatus',
      text: t('market.table.headers.status'),
      editable: false,
      align: 'center',
      headerAlign: 'center',
      formatter: (cellContent, row) => <div>{row.reservationStatus.toLowerCase() === 'unknown' ? '' : row.reservationStatus}</div>
    },
    {
      dataField: 'upTillDate',
      text: t('market.table.headers.upTillDate'),
      align: 'center',
      headerAlign: 'center',
      formatter: (cellContent, row) => <div>{cellContent && cellContent.split('T')[0]}</div>,
      headerStyle: (colum, colIndex) => {
        return { width: '16%', textAlign: 'center' }
      }
    },
    {
      text: 'operator',
      align: 'center',
      headerAlign: 'center',
      editable: true,
      formatter: (cellContent, row) => <Button
        color='primary' onClick={() => {
          history.push({ pathname: '/users-profile', search: '?operatorId=' + row.operatorId })
        }}
                                       >Profil operatora
      </Button>
    },
    {
      text: 'ocena operatora',
      align: 'center',
      headerAlign: 'center',
      editable: true,
      formatter: (cellContent, row) => row.avgOperatorRating
        ? <div className='d-flex justify-content-center' id={'avg-rating-' + row.id}>
          <ReactStars
            count={5}
            edit={false}
            isHalf
            value={Math.ceil(row.avgOperatorRating)}
            size={24}
            activeColor='#ffd700'
          />
          <UncontrolledTooltip
            flip
            target={'avg-rating-' + row.id}
          >
            {row.avgOperatorRating.toFixed(3)}
          </UncontrolledTooltip>
        </div>
        : 'Brak danych'
    },
    {
      text: t('market.table.headers.accept-resign'),
      align: 'center',
      headerAlign: 'center',
      editable: true,
      formatter: (cellContent, row) => {
        return (
          <Button
            disabled={isButtonDisabled(row, state.marketPage.propositions, accounts, state.marketPage.selectedOrder.clientId, isOperator)}
            color={row.reservationStatus.toLowerCase() === 'reserved' ? 'danger' : 'success'}
            onClick={() => {
              row.reservationStatus.toLowerCase() === 'unknown' || row.reservationStatus.toLowerCase() === 'unreserved'
                ? reserveOrderAndProposition(dispatch, row.id, state.marketPage.selectedOrder.id)
                : unreserveProposition(dispatch, row.id, state.marketPage.selectedOrder.id)
            }}
          >
            {row.reservationStatus.toLowerCase() === 'reserved' ? t('market.table.buttons.resign') : t('market.table.buttons.accept')}
          </Button>
        )
      }
    }
  ]

  return (
    <div className='content'>
      {isAdmin && <Form>
        <FormGroup>
          <Label>
            {t('market.showPropositionId')}
            <input
              type='checkbox'
              style={{ marginLeft: '0.5rem' }}
              onChange={() => {
                setIsIdHidden(!isIdHidden)
              }}
            />{' '}
          </Label>
        </FormGroup>
      </Form>}
      <BootstrapTable
        bootstrap4
        keyField='id'
        data={state.marketPage.propositions.sort((a, b) => parseInt(a.description.split(' ')[0]) < parseInt(b.description.split(' ')[0]) ? 1 : -1)}
        columns={columns}
        striped
        hover
        bordered={false}
      />
    </div>
  )
}

export default PropositionList
