import { landRegisterService, operatFilesService } from '../services'
import { operatFilesConstants, modalConstants, modalTypeConstants } from '../constants'
import { v4 as uuidv4 } from 'uuid'
import { modalActions } from './modal.actions'

export const operatFilesActions = {
  updateValue,
  editOperat,
  generateNew,
  removeInfo,
  loadEmptyOperat,
  getFormatedFilesByOperat,
  loadOperatInput,
  loadOperatCSV,
  addLineToCSV,
  loadOperatEmptyInput,
  removeLineFromCSV,
  loadOperatType,
  loadOperatEmptyPhoto,
  loadFileToGallery,
  addLinesToCSVFromCSV,
  clearOperatState,
  removePhotos,
  removePhotoFiles,
  downloadFromEkw
}

function clearOperatState (dispatch) {
  dispatch({
    type: operatFilesConstants.FILES_CLEAR_STATE
  })
}

function downloadFromEkw (dispatch, code, name) {
  code = code.replaceAll('/', ';')
  modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: 'Prosz czeka trwa wczytywanie danych z ksigi wieczystej' })
  landRegisterService.get(code).then(ekw => {
    dispatch({
      type: operatFilesConstants.DOWNLOAD_FROM_EKW,
      payload: {
        code,
        name,
        ekw
      }
    })
  }).catch(err => modalActions.showLoadingScreen(dispatch, modalTypeConstants.ERROR_MESSAGE, { message: err }))
}

function loadFileToGallery (dispatch, id, files) {
  dispatch({
    type: operatFilesConstants.FILES_LOAD_PHOTO_FILE_TO_GALLERY,
    payload: { id, files }
  })
}

function removePhotos (dispatch, id) {
  dispatch({
    type: operatFilesConstants.REMOVE_PHOTOS,
    payload: { id }
  })
}

function removePhotoFiles (dispatch, id) {
  dispatch({
    type: operatFilesConstants.REMOVE_PHOTO_FILES,
    payload: { id }
  })
}

function loadOperatEmptyInput (dispatch, input) {
  dispatch({
    type: operatFilesConstants.FILES_LOAD_EMPTY_INPUT,
    payload: input
  })
}

function loadOperatEmptyPhoto (dispatch, photo) {
  dispatch({
    type: operatFilesConstants.FILES_LOAD_EMPTY_PHOTO,
    payload: photo
  })
}

function loadOperatType (dispatch, data) {
  dispatch({
    type: operatFilesConstants.FILES_LOAD_OPERAT_TYPE,
    payload: data
  })
}

function updateValue (dispatch, e, id, idx) {
  dispatch({
    type: operatFilesConstants.FILES_PUT_OPERAT,
    payload: {
      event: e.target.value,
      id: id,
      idx: idx
    }
  })
}

function getFormatedFilesByOperat (dispatch, row) {
  return operatFilesService.getFormatedFilesByOperat(row.id).then(
    files => {
      dispatch({
        type: operatFilesConstants.FILES_GET_BY_OPERAT,
        payload: {
          id: row.id,
          operatInfo: row,
          files: files,
          operatTypeId: row.operatTypeId
        }
      })
    }
  ).then((res) => {
    dispatch({
      type: modalConstants.MODAL_HIDE_LOADING_SCREEN
    })
    return row.operatTypeId
  })
}

function loadOperatInput (dispatch, url) {
  operatFilesService.getFile(url).then(
    input => {
      dispatch({
        type: operatFilesConstants.FILES_GET_INPUT,
        payload: {
          input: input.input
        }
      })
    }
  ).then(() => {
    dispatch({
      type: modalConstants.MODAL_HIDE_LOADING_SCREEN
    })
  })
}

function loadOperatCSV (dispatch, url) {
  operatFilesService.getFile(url).then(
    csv => {
      dispatch({
        type: operatFilesConstants.FILES_GET_CSV,
        payload: {
          csv: csvJSON(csv)
        }
      })
    }
  ).then(() => {
    dispatch({
      type: modalConstants.MODAL_HIDE_LOADING_SCREEN
    })
  })
}

function loadEmptyOperat (dispatch, id) {
  dispatch({
    type: operatFilesConstants.FILES_LOAD_EMPTY_OPERAT,
    payload: {
      id: id,
      disabled: false
    }
  })
}

function editOperat (dispatch) {
  dispatch({
    type: operatFilesConstants.FILES_EDIT_OPERAT,
    payload: {
      disabled: false
    }
  })
}

function generateNew (dispatch, value, id) {
  dispatch({
    type: 'GENERATE_OPERAT',
    payload: { value, id }
  })
}

function removeInfo (dispatch, id) {
  dispatch({
    type: 'REMOVE_OPERAT_FROM_GENERATED',
    payload: id
  })
}

function addLineToCSV (dispatch) {
  dispatch({
    type: operatFilesConstants.FILES_ADD_LINE_TO_CSV,
    payload: { newRow: generateNewRowForCSV() }
  })
}

function addLinesToCSVFromCSV (dispatch, rows) {
  dispatch({
    type: operatFilesConstants.FILES_ADD_LINES_TO_CSV_FROM_CSV,
    payload: { newRows: generateNewRowsForCSVFromCSV(rows) }
  })
}

function removeLineFromCSV (dispatch, rows) {
  dispatch({
    type: operatFilesConstants.FILES_REMOVE_LINE_FROM_CSV,
    payload: { rows }
  })
}

function generateNewRowForCSV () {
  return {
    Data: 'data',
    CenaTransakcyjna: 'cena transakcyjna',
    RodzajRynku: 'rodzaj rynku',
    UdzialWPrawie: 'udzia w prawie',
    PowierzchniaLokalu: 'powierzchnia lokalu',
    Adres: 'adres',
    KondygnacjaLokalu: 'kondygnacja lokalu',
    KondygnacjaBudynku: 'kondygnacja budynku',
    LiczbaIzb: 'liczba izb',
    id: uuidv4()
  }
}

function generateNewRowsForCSVFromCSV (rows) {
  const lines = []
  rows.forEach(element => {
    const row = element.split(';')
    lines.push({
      Data: row[0],
      CenaTransakcyjna: row[1],
      RodzajRynku: row[2],
      UdzialWPrawie: row[3],
      PowierzchniaLokalu: row[4],
      Adres: row[5],
      KondygnacjaLokalu: row[6],
      KondygnacjaBudynku: row[7],
      LiczbaIzb: row[8].replace('/r', ''),
      id: uuidv4()
    })
  })
  return lines
}

function csvJSON (csv) {
  const lines = csv.split('\n')
  const result = []
  const headers = lines[0].split(',')[0].split(';')

  for (let i = 1; i < lines.length; i++) {
    const obj = {}
    const currentline = lines[i].split(';')
    for (let j = 0; j < headers.length; j++) {
      obj[headers[j].trim()] = currentline[j]
    }
    obj.id = uuidv4()
    result.push(obj)
  }

  return result
}
