import { useMsal } from '@azure/msal-react'
import React, { useState } from 'react'
import { useTranslation } from 'react-i18next'
import { Button, Form, FormGroup, Input, Label, UncontrolledTooltip, UncontrolledPopover, PopoverBody, Col } from 'reactstrap'
import { MapWithMarker } from '../../views/Maps'

export const NewOrderForm = (props) => {
  const { t } = useTranslation('common')
  const { accounts } = useMsal()
  const [name, setName] = useState('')
  const [description, setDescription] = useState('')
  const [address, setAddress] = useState('')
  const [authorizedExperts, setAuthorizedExperts] = useState(false)
  const [latlng, setLatlng] = useState({ lat: 0, lng: 0 })
  const [locationPostCode, setLocationPostCode] = useState('')
  const [shouldBeValidated, setShouldBeValidated] = useState(true)
  const resetForm = () => {
    setName('')
    setDescription('')
    setAddress('')
    setAuthorizedExperts(false)
    setLatlng({ lat: 0, lng: 0 })
  }
  const addOrder = (e) => {
    e.preventDefault()
    props.newOrderButton.current.click()
    resetForm()
    const order = {
      name: name,
      description: description,
      location: address,
      requiresZbpList: authorizedExperts,
      latitudeLongitude: `${latlng.lat},${latlng.lng}`,
      locationPostCode,
      clientId: accounts[0].idTokenClaims.oid,
      shouldBeValidated
    }
    props.updateMarket()
    props.onSubmit(order)
  }

  function checkAllRequiresFields () {
    return name === '' || description === '' || locationPostCode === ''
  }

  return (
    <div>
      <Form
        onSubmit={(e) => addOrder(e)}
      >
        <FormGroup>
          <Label for='orderName'>{t('market.table.headers.name')}</Label>
          <Input
            type='text'
            name='orderName'
            id='orderName'
            value={name}
            onChange={(e) => {
              setName(e.target.value)
            }}
            required
          />
          <UncontrolledTooltip
            placement='top'
            target='orderName'
          >
            {t('market.newOrder.namePlaceholder')}
          </UncontrolledTooltip>
        </FormGroup>
        <FormGroup>
          <Label for='description'>{t('market.table.headers.description')}</Label>
          <Input
            type='textarea'
            name='description'
            id='description'
            value={description}
            onChange={(e) => {
              setDescription(e.target.value)
            }}
            required
          />
          <UncontrolledTooltip
            placement='top'
            target='description'
          >
            {t('market.newOrder.descriptionPlaceholder')}
          </UncontrolledTooltip>
        </FormGroup>
        <FormGroup>
          <Label for='postal-code'>{t('market.newOrder.postalCode')}</Label>
          <Input
            type='text'
            name='postal-code'
            id='new-order-postal-code'
            value={locationPostCode}
            pattern='\d{2}-\d{3}$'
            onChange={(e) => {
              setLocationPostCode(e.target.value)
            }}
            required
          />
        </FormGroup>
        <FormGroup>
          <Label for='address'>{t('common.userInfo.address')}</Label>
          <Input
            type='text'
            name='address'
            id='address'
            value={address}
            onChange={(e) => {
              setAddress(e.target.value)
            }}
          />
          <UncontrolledTooltip
            placement='top'
            target='address'
          >
            {t('market.newOrder.addressPlaceholder')}
          </UncontrolledTooltip>
        </FormGroup>
        <FormGroup>
          <Button
            className='long-btn' id='UncontrolledPopoverMAP'
            type='button' color='success'
          >Dodaj lokalizacj na mapie
          </Button>
          <UncontrolledPopover
            placement='right'
            target='UncontrolledPopoverMAP'
            trigger='legacy'
          >
            <PopoverBody>
              <MapWithMarker
                locationInfo={{ address, locationPostCode, latlng }}
                setCoords={(cords) => { setLatlng(cords) }}
              />
            </PopoverBody>
          </UncontrolledPopover>
        </FormGroup>
        <Button
          color='success'
          disabled={checkAllRequiresFields()}
        >
          {t('common.add')}
        </Button>
        <FormGroup>
          <Col sm={{ size: 10 }}>
            <FormGroup>
              <Label for='authorizedExperts' sm={10}>
                <Input
                  type='checkbox'
                  name='authorizedExperts'
                  id='authorizedExperts'
                  value={authorizedExperts}
                  onChange={(e) => {
                    setAuthorizedExperts(e.target.checked)
                  }}
                />
                {t('market.table.headers.authorizedExperts')}
              </Label>
            </FormGroup>
          </Col>
        </FormGroup>
        <FormGroup>
          <Col sm={{ size: 10 }}>
            <FormGroup>
              <Label for='aoperatValidation' sm={10}>
                <Input
                  type='checkbox'
                  name='aoperatValidation'
                  id='aoperatValidation'
                  checked={shouldBeValidated}
                  onChange={(e) => {
                    setShouldBeValidated(e.target.checked)
                  }}
                />
                {t('market.table.headers.aoperatValidation')}
              </Label>
            </FormGroup>
          </Col>
        </FormGroup>
      </Form>
    </div>
  )
}

export default NewOrderForm
