import { modalActions } from 'actions'
import { loadingMessagesConstants, modalTypeConstants } from '../../constants'
import { operatorService } from 'services'
import { SET_TRAININGS } from './constants'
export const addTraining = async (event, dispatch, operatorId) => {
  event.preventDefault()
  modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.ADD_TRAINING })
  const title = event.target.elements['training-title'].value
  const url = event.target.elements['training-url'].value
  const training = { title, url, operatorId }
  operatorService.addTraining(training).then(async () => await getTrainings(dispatch, operatorId)).finally(() => modalActions.hideLoadingScreen(dispatch))
}

export const deleteTraining = (dispatch, id, operatorId) => {
  modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.DELETE_TRAINING })
  operatorService.deleteTraining(id).then(() => getTrainings(dispatch, operatorId)).catch(() => setTrainings(dispatch, [])).finally(() => modalActions.hideLoadingScreen(dispatch))
}

export const setTrainings = (dispatch, data) => dispatch({ type: SET_TRAININGS, payload: data })

export const getTrainings = async (dispatch, operatorId) => {
  await operatorService.getOperatorTrainings(operatorId).then(res => setTrainings(dispatch, res.map(x => ({ id: x.id, title: x.title, url: x.url }))))
}
