import React, { useEffect, useContext } from 'react'
import {
  Card,
  Row,
  Col,
  UncontrolledCollapse,
  Button,
  CardBody
} from 'reactstrap'
import { useMsal } from '@azure/msal-react'
import ReadonlyTable from 'components/Tables/ReadonlyTable'
import OperatorTrainingForm from 'components/OperatorTrainingForm'
import { addTraining, deleteTraining, getTrainings } from './actions'
import { AppContext } from 'contexts/context'
function OperatorTrainingsTab (props) {
  const [state, dispatch] = useContext(AppContext)
  const { accounts } = useMsal()
  useEffect(() => {
    getTrainings(dispatch, accounts[0].idTokenClaims.oid)
  }, [accounts, dispatch])
  return (
    <>
      <Row>
        <Col md='8'>
          <Card>
            <CardBody>
              <Button color='success' id='show-operator-training-form-btn' style={{ width: '11rem' }}>Nowe szkolenie</Button>
              <UncontrolledCollapse toggler='#show-operator-training-form-btn'>
                <Card style={{ boxShadow: '6px 6px 10px 2px rgb(0 0 0 / 15%)' }}>
                  <CardBody>
                    <OperatorTrainingForm onSubmit={(e) => addTraining(e, dispatch, accounts[0].idTokenClaims.oid)} />
                  </CardBody>
                </Card>
              </UncontrolledCollapse>
              <br />
              <Button color='success' id='show-operator-trainings-btn' style={{ width: '11rem' }}>Szkolenia</Button>
              <UncontrolledCollapse toggler='#show-operator-trainings-btn'>
                <Card style={{ boxShadow: '6px 6px 10px 2px rgb(0 0 0 / 15%)' }}>
                  <CardBody>
                    {state.userPage.trainings && (state.userPage.trainings.length === 0
                      ? 'Brak szkole'
                      : <ReadonlyTable
                          headers={['Tytu', 'Adres']}
                          data={state.userPage.trainings}
                          deleteAction={(id) => deleteTraining(dispatch, id, accounts[0].idTokenClaims.oid)}
                        />)}
                  </CardBody>
                </Card>
              </UncontrolledCollapse>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </>
  )
}

export default OperatorTrainingsTab
