import { SET_TRAININGS } from './constants'
const initialState = {
  trainings: []
}

const userPage = (state = initialState, action) => {
  switch (action.type) {
    case SET_TRAININGS: {
      return {
        ...state,
        trainings: action.payload
      }
    }
    default:
      return state
  }
}

export default userPage
