import { callForHeader } from '../utils/HeadersCall'
const apiUrl = process.env.REACT_APP_API_URL

export const orderProposition = {
  sendProposition,
  getPropositionById,
  reserveOrderAndProposition,
  unReserveProposition
}

async function sendProposition (data) {
  const requestOptions = {
    method: 'POST',
    headers: await callForHeader('json'),
    body: JSON.stringify(data)
  }

  return window.fetch(`${apiUrl}/OrderProposition`, requestOptions).then(handleResponse)
}

async function getPropositionById (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader('json')
  }

  return window.fetch(`${apiUrl}/OrderProposition/orderid/${id}`, requestOptions).then(handleResponse)
}

async function reserveOrderAndProposition (propositionId) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/OrderProposition/id/${propositionId}/reserve`, requestOptions).then(handleResponse)
}

async function unReserveProposition (propositionId) {
  const requestOptions = {
    method: 'PUT',
    headers: await callForHeader('json')
  }

  return await window.fetch(`${apiUrl}/OrderProposition/id/${propositionId}/unreserve`, requestOptions).then(handleResponse)
}

function handleResponse (response) {
  return response.text().then(text => {
    let data = ''
    if (text.startsWith('Data')) {
      data = text
    } else {
      data = text && JSON.parse(text)
    }
    if (!response.ok) {
      if (response.status === 401) {
        // TODO auto logout if 401 response returned from api
      }
      if (response.status === 404) {
        // TODO auto logout if 404 response returned from api
        return []
      }

      const error = (data && data.message) || response.statusText
      return Promise.reject(error)
    }

    return data
  })
}
