import React, { useContext, useState } from 'react'
import {
  Card,
  CardBody,
  Button,
  Collapse
} from 'reactstrap'
import { AppContext } from '../../contexts/context'

export function InfoForAdmin () {
  const [isInfoVisible, setIsInfoVisible] = useState(false)
  const [state] = useContext(AppContext)

  return (
    <>
      <Button style={{ width: '16rem' }} color='info' onClick={() => setIsInfoVisible(!isInfoVisible)}>Informacje</Button>

      <Collapse isOpen={isInfoVisible}>
        <Card>
          <CardBody>
            {state.operatFiles.operatInfo && Object.keys(state.operatFiles.operatInfo).map(x => state.operatFiles.operatInfo[x] && (<p>{x + ': ' + state.operatFiles.operatInfo[x]}</p>))}
          </CardBody>
        </Card>
      </Collapse>
    </>
  )
}
