function OwnCertificate () {
  const [state, dispatch] = useContext(AppContext)
  const history = useHistory()
  const [certificatePassword, setCertificatePassword] = useState('')
  return (
    <>
      <ModalHeader>Wybierz sposb w jaki chcesz podpisa operat</ModalHeader>
      <ModalBody>
        <Row>
          <Col>
            <FormGroup>
              <Input
                type='password'
                name='certificatePassword'
                id='certificatePassword'
                value={certificatePassword}
                placeholder='Haso do certyfikatu'
                onChange={(e) => setCertificatePassword(e.target.value)}
              />
            </FormGroup>
          </Col>
          <Col>
            <Button
              onClick={() => {
                modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: loadingMessagesConstants.SING_OPERAT })
                operatService.signPdf(state.operatFiles.id, { password: certificatePassword }).then(() => {
                  modalActions.hideLoadingScreen(dispatch)
                  getOperats(dispatch, history.location)
                }).catch(error => {
                  modalActions.showLoadingScreen(dispatch, modalTypeConstants.ERROR_MESSAGE, { message: error })
                })
              }}
              style={{ marginTop: '0px' }}
              color='success'
            >
              Podpisz operat
            </Button>
          </Col>
        </Row>
        <Row>
          <Col>Jeeli nie posiadasz certyfikatu: <Button color='link' onClick={() => modalActions.showLoadingScreen(dispatch, modalTypeConstants.NEW_CERTIFICATE, {})}>Przejd do generowania certyfikatu</Button></Col>
        </Row>
        <Row>
          <Col>
            <Button color='link' onClick={() => modalActions.showLoadingScreen(dispatch, modalTypeConstants.SIGN_OPERAT, {})}>Powrt</Button>
          </Col>
        </Row>
      </ModalBody>
    </>
  )
}

