function OperatList () {
  const [state, dispatch] = useContext(AppContext)
  const currentOperat = state.modal.data.operat
  console.log(state.operat.operats)
  const operats = state.operat.operats
    .filter(x => x.operatTypeId === currentOperat.operatTypeId && x.id !== currentOperat.id)
  const listGroup = (
    <ListGroup style={{ overflow: 'scroll', maxHeight: '67vh' }}>
      {operats
        .map((operat) =>
          <ListGroupItem
            key={operat.id}
            action
            onClick={async () => {
              const result = await confirm({
                title: 'Ostrzeenie',
                message: 'Czy na pewno chcesz skopiowa dane z tego operatu operatu? Dane zostan nadpisane',
                confirmText: 'Kopiuj',
                cancelText: 'Wr',
                confirmColor: 'success',
                cancelColor: 'link text-primary'
              })
              if (result) {
                const handleResponse = () => {
                  modalActions.hideLoadingScreen(dispatch)
                  operatFilesActions.getFormatedFilesByOperat(dispatch, currentOperat)
                }
                modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { message: 'Trwa kopiowanie danych z operatu' })
                switch (state.modal.data.dataType) {
                  case 'params':
                    operatFilesService.copyOperatParams(operat.id, currentOperat.id).then(() => {
                      handleResponse()
                    })
                    break
                  case 'csv':
                    operatFilesService.copyOperatCsv(operat.id, currentOperat.id).then(() => {
                      handleResponse()
                    })
                    break
                  case 'photos':
                    operatFilesService.copyOperatPhotos(operat.id, currentOperat.id).then(() => {
                      handleResponse()
                    })
                    break
                  default:
                }
              }
            }}
            tag='a'
          >
            {operat.name}
          </ListGroupItem>
        )}
    </ListGroup>
  )
  return (
    <>
      <ModalHeader>Wybierz operat, z ktrego chcesz skopiowa dane</ModalHeader>
      <ModalBody>
        {operats.length > 0
          ? listGroup
          : 'Brak operatw, z ktrych mona by skopiowa dane'}
      </ModalBody>
      {createModalFooter(dispatch, true)}
    </>
  )
}
