import React, { useEffect, useContext } from 'react'
import { useMsal } from '@azure/msal-react'
import { AppContext } from '../../contexts/context'
import { clientService } from '../../services'
import { modalActions } from '../../actions'
import { modalTypeConstants } from '../../constants'

function EmptyUser (props) {
  const { instance } = useMsal()
  const [state, dispatch] = useContext(AppContext)

  useEffect(() => {
    if (state.user.userType === '') {
      const account = instance.getActiveAccount()

      clientService.getAdminDataById(account.idTokenClaims.oid).then(exist => {
        if (!exist) {
          const newClient = {
            id: account.idTokenClaims.oid,
            name: account.name,
            mail: account.idTokenClaims.emails[0],
            address: `${account.idTokenClaims.streetAddress}, ${account.idTokenClaims.postalCode}, ${account.idTokenClaims.city}`
          }
          modalActions.showLoadingScreen(dispatch, modalTypeConstants.LOADING, { newClient, message: 'Trwa tworzenie uytkownika' })
          clientService.createNewClient(newClient).then(() => {
            props.history.push('/start')
            modalActions.hideLoadingScreen(dispatch)
          })
        }
      })
    }
  }, [state.user.userType, instance, dispatch, props.history])

  return (
    <div />
  )
}

export default EmptyUser
