import { callForHeader } from 'utils/HeadersCall'

const apiUrl = process.env.REACT_APP_API_URL

export const userService = {
  getUserType,
  getClients,
  getOperators,
  getAdmins
}
const appendSearchParamsToUrl = (url, params) => {
  const newUrl = new URL(url)
  if (params) {
    newUrl.search = new URLSearchParams(params).toString()
  }
  return newUrl
}
async function getUserType (id) {
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader()
  }

  return await window.fetch(`${apiUrl}/User/id/${id}`, requestOptions).then(handleResponse)
}

async function getClients (params) {
  const url = appendSearchParamsToUrl(`${apiUrl}/User/promote/clients`, params)
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader()
  }

  return await window.fetch(url, requestOptions).then(handleResponse)
}

async function getOperators (params) {
  const url = appendSearchParamsToUrl(`${apiUrl}/User/promote/operators`, params)
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader()
  }

  return await window.fetch(url, requestOptions).then(handleResponse)
}

async function getAdmins (params) {
  const url = appendSearchParamsToUrl(`${apiUrl}/User/promote/admins`, params)
  const requestOptions = {
    method: 'GET',
    headers: await callForHeader()
  }

  return await window.fetch(url, requestOptions).then(handleResponse)
}

function handleResponse (response) {
  return response.text().then(text => {
    const data = text && JSON.parse(text)
    if (!response.ok) {
      const error = (data && data.message) || response.statusText
      return Promise.reject(error)
    }
    const pageCount = response.headers.get('X-Page-Count')
    pageCount && localStorage.setItem('pageCount', pageCount)
    return data
  }).then(data => { return data })
}
